import React, { useState, useEffect } from 'react';
import {
    Box,
    Container,
    Typography,
    Paper,
    Alert,
    Divider
} from '@mui/material';
import { IDispatcher, useProxyWatch } from '@fanfanlo';
import { user } from '@app/model';
import { autoWebViewJs } from '@droid/android';

import FallDetectionWrapper from './FallDetectionWrapper';
import ShakeDetectionWrapper from './ShakeDetectionWrapper';
import { SENSOR_MONITORING_STATE_CHANGED } from './constants';

function EmergencyCallSettings({dispatcher}:{dispatcher:IDispatcher}){
    // UI
    const [fallExpanded, setFallExpanded] = useState(false);
    const [shakeExpanded, setShakeExpanded] = useState(false);
    
    // WrapperUI

    // 
    const [userToken] = useProxyWatch(user, 'data.storeData.user_token', user.data.storeData.user_token);
    const isLoggedIn = !!userToken;

    // 
    const handleToggleExpanded = (
        type: 'fall' | 'shake', 
        expanded: boolean
    ) => {
        if (expanded) {
            // 
            setFallExpanded(type === 'fall');
            setShakeExpanded(type === 'shake');
        } else {
            // 
            switch (type) {
                case 'fall':
                    setFallExpanded(false);
                    break;
                case 'shake':
                    setShakeExpanded(false);
                    break;
            }
        }
    };

    // Wrapper

    return (
        <Box maxWidth="md" sx={{ py: 3 }}>

            {/*  */}
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
                {/*  */}
                <FallDetectionWrapper
                    title=" "
                    isExpanded={fallExpanded}
                    onToggleExpanded={(expanded) => handleToggleExpanded('fall', expanded)}
                    animationType="fall"
                    dispatcher={dispatcher}
                />

                {/*  */}
                {/* <ShakeDetectionWrapper
                    title=" "
                    isExpanded={shakeExpanded}
                    onToggleExpanded={(expanded) => handleToggleExpanded('shake', expanded)}
                    animationType="shake"
                /> */}
            </Box>

        </Box>
    );
};

export default EmergencyCallSettings;