import { trackingPrint } from '@app/model';
import { AppShell, TabbarContainer } from '@app/ui';
import { autoWebViewJs } from '@droid/android';
import { Dispatcher, IDispatcher, nextPreload } from '@fanfanlo';
import { Box, Button, Grid2, Link, IconButton } from '@mui/material';
import { InviteContactDialog } from '@src/components/dialogs/InviteContactDialog';
import EmergencyStopButton from '@src/components/emergency-audio-control/EmergencyStopButton';
import { EmergencyTTSSettings } from '@src/components/emergency-tts-settings';
import { ErrorDisplay } from '@src/components/error-display/ErrorDisplay';
import MeetingList from '@src/components/meeting-list/MeetingList';
import { NavBar } from '@src/components/navbar/NavBar';
import { EmergencyCallSettings } from '@src/components/pages/home';
import AddWidgetButton from '@src/components/pages/home/components/AddWidgetButton';
import AddSecurityWidgetButton from '@src/components/pages/home/components/AddSecurityWidgetButton';
import PendingInvitationsList from '@src/components/PendingInvitationsList/PendingInvitationsList';
import PreAlarmConfig2 from '@src/components/pre-alarm-config2';
import { ListenShowPrivacyContent } from '@src/components/privacies/ListenShowPrivacyContent';
import SensorMonitor from '@src/components/sensor-monitor/SensorMonitor';
import ShakeConfigUI from '@src/components/shakeconfig/ShakeConfigUI';
import { TestEmergencyCall } from '@src/components/test/TestEmergencyCall';
import { useRouter } from 'next/router';
import React, { useState } from 'react';
import { useFontSize } from '@src/contexts/FontSizeContext';

function Content() {
  const [openInviteDialog, setOpenInviteDialog] = useState(false);

  const router = useRouter();

  React.useEffect(() => {
    nextPreload(router, '/intro');
    nextPreload(router, '/how-to-use');
  }, []);

  return (
    <Box sx={{ my: 2, p: 2 }}>
      <ErrorDisplay />
      {/*  -  */}
      <EmergencyStopButton fullWidth size="large" />

      <Box>
        <Link href="/how-to-use" style={{ textDecoration: 'none' }}>
          </Link>
      </Box>
      <ListenShowPrivacyContent type="main" />
      {/* <InvitationManager /> */}
      {/* <MeetingList maxItemLength={3} /> */}
      <PendingInvitationsList />

      <Grid2 container spacing={2} sx={{ m: 2 }}>
        <Grid2 size={6}>
          <Button variant="contained" onClick={() => router.push('/intro')}>
            
          </Button>
        </Grid2>
        <Grid2 size={6}>
          <Box sx={{ display: 'flex', justifyContent: 'flex-end' }}>
            <Button
              // fullWidth
              variant="contained"
              onClick={() => {
                router.push('/how-to-use');
              }}>
              
            </Button>
          </Box>
        </Grid2>
      </Grid2>
      <PreAlarmConfig2 />
      <EmergencyTTSSettings />
      {/* <PreAlarmConfigComponent /> */}

      {/* <JpushIsInitialized /> */}

      <Grid2 container sx={{ mt: 2, mb: 2 }}>
        <Grid2 size={6}>
          <TestEmergencyCall />
        </Grid2>
        <Grid2 size={6}>
          <Box sx={{ display: 'flex', justifyContent: 'flex-end' }}>
            <EmergencyStopButton variant="button" />
          </Box>
        </Grid2>
      </Grid2>
      
      {/*  */}
      <AddWidgetButton 
        onSuccess={() => {
          console.log('');
        }}
        onError={(error) => {
          console.error(':', error);
        }}
      />
      
      
      {/* gemini */}
      {/* <SensorPermissions /> */}
{/*  */}
      <ShakeConfigUI />

      <SensorMonitor />
      {/*  */}
      {/* <UnifiedMonitoringControl /> */}

      {/*   -  */}

      <Grid2 container spacing={2} sx={{ m: 2 }}>
        <Grid2 size={6}>
          <Button 
            variant="contained" 
            onClick={() => {
              // 
              trackingPrint({params:{
                type:"",
                title:"",
                subtitle:"-",
                extra:{
                  timestamp: Date.now(),
                  page: "home"
                }
              }});
              setOpenInviteDialog(true);
            }}
          >
            
          </Button>
        </Grid2>
        <Grid2 size={6}>
          <Box sx={{ display: 'flex', justifyContent: 'flex-end' }}>
            <Button
              fullWidth
              variant="contained"
              onClick={() => {
                router.push('/contacts');
              }}>
              
            </Button>
          </Box>
        </Grid2>
      </Grid2>
      {openInviteDialog && <InviteContactDialog 
        open={openInviteDialog} 
        onClose={() => {
          // 
          trackingPrint({params:{
            type:"",
            title:"",
            subtitle:"-",
            extra:{
              timestamp: Date.now(),
              page: "home"
            }
          }});
          setOpenInviteDialog(false);
        }} 
      />}

      {/* <MeetingRoom5
        roomName="test-room"
        userName={`user-${Math.random().toString(36).substring(2, 8)}`}
        livekitConfig={mainModel.appConfig.livekit}
        // role="initiator" // Add role for testing
      />
       */}

      {/* TTS */}
      {/* <TTSRecorder /> */}
      {/*  */}
      {/* <ShortcutConfig /> */}

      <Box sx={{ mt: 2, mb: 2 }}>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            router.push('/shortcut');
          }}>
          
          {/* <Link href={"/shortcut"}></Link> */}
        </Button>
      </Box>
      {/* <EmergencyAudioControl /> */}
      {/*  */}
      {/* <EmergencyStopButton variant="fab" /> */}

      {/* <Box>
      <Link href={"/logs"}></Link>
      <IMOpenPageLink />
</Box> */}

      <Box sx={{ mt: '2rem' }}></Box>
    </Box>
  );
}


function Content2(){
  const [dispatcher] = useState<IDispatcher>(new Dispatcher())
  const router = useRouter();
  const [openInviteDialog, setOpenInviteDialog] = useState(false);
  const { openDialog, showInHome } = useFontSize();
  return (
    <Box sx={{ position: 'relative' }}>
      {showInHome && (
        <IconButton
          onClick={(e) => {
            e.preventDefault();
            openDialog(true);
          }}
          sx={{
            position: 'fixed',
            top: 24,
            right: 24,
            zIndex: 9999,
            backgroundColor: 'primary.main',
            color: 'primary.contrastText',
            '&:hover': {
              backgroundColor: 'primary.dark',
              transform: 'scale(1.1)',
            },
            width: 60,
            height: 60,
            boxShadow: 3,
            fontSize: '2rem',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            transition: 'all 0.3s ease',
          }}
          aria-label=""
          title=""
        >
          
        </IconButton>
      )}
      
      <ListenShowPrivacyContent type="main" dispatcher={dispatcher} />
      <ErrorDisplay />
      <PendingInvitationsList />
    <EmergencyCallSettings dispatcher={dispatcher} />
    
          <TestEmergencyCall />
      <Grid2 container spacing={3} sx={{ mt: 2, px: 2 }}>
        {/*  */}
        <Grid2 size={6}>
          <Button 
            variant="contained" 
            fullWidth
            onClick={() => {
              // 
              trackingPrint({params:{
                type:"",
                title:"",
                subtitle:"-",
                extra:{
                  timestamp: Date.now(),
                  page: "home"
                }
              }});
              setOpenInviteDialog(true);
            }}
            sx={{
              backgroundColor: '#4CAF50',
              color: 'white',
              fontSize: '16px',
              fontWeight: 'bold',
              minHeight: '48px',
              borderRadius: '24px',
              boxShadow: '0 4px 16px rgba(76, 175, 80, 0.3)',
              textTransform: 'none',
              position: 'relative',
              overflow: 'hidden',
              '&:hover': {
                backgroundColor: '#45a049',
                transform: 'translateY(-1px)',
                boxShadow: '0 6px 20px rgba(76, 175, 80, 0.4)',
              },
              '&:active': {
                transform: 'translateY(0px)',
                boxShadow: '0 2px 8px rgba(76, 175, 80, 0.3)',
              },
              '&::before': {
                content: '""',
                position: 'absolute',
                top: '50%',
                left: '50%',
                width: '0',
                height: '0',
                borderRadius: '50%',
                backgroundColor: 'rgba(255, 255, 255, 0.2)',
                transform: 'translate(-50%, -50%)',
                transition: 'width 0.3s, height 0.3s',
              },
              '&:hover::before': {
                width: '100%',
                height: '100%',
              }
            }}
            startIcon={
              <Box sx={{ 
                fontSize: '20px'
              }}>
                
              </Box>
            }
          >
            
          </Button>
        </Grid2>

        {/*  */}
        <Grid2 size={6}>
          <Button
            fullWidth
            variant="contained"
            onClick={() => {
              router.push('/contacts');
            }}
            sx={{
              backgroundColor: '#2196F3',
              color: 'white',
              fontSize: '16px',
              fontWeight: 'bold',
              minHeight: '48px',
              borderRadius: '24px',
              boxShadow: '0 4px 16px rgba(33, 150, 243, 0.3)',
              textTransform: 'none',
              position: 'relative',
              overflow: 'hidden',
              '&:hover': {
                backgroundColor: '#1976d2',
                transform: 'translateY(-1px)',
                boxShadow: '0 6px 20px rgba(33, 150, 243, 0.4)',
              },
              '&:active': {
                transform: 'translateY(0px)',
                boxShadow: '0 2px 8px rgba(33, 150, 243, 0.3)',
              },
              '&::before': {
                content: '""',
                position: 'absolute',
                top: '50%',
                left: '50%',
                width: '0',
                height: '0',
                borderRadius: '50%',
                backgroundColor: 'rgba(255, 255, 255, 0.2)',
                transform: 'translate(-50%, -50%)',
                transition: 'width 0.3s, height 0.3s',
              },
              '&:hover::before': {
                width: '100%',
                height: '100%',
              }
            }}
            startIcon={
              <Box sx={{ 
                fontSize: '20px'
              }}>
                
              </Box>
            }
          >
            
          </Button>
        </Grid2>
      </Grid2>
      {openInviteDialog && <InviteContactDialog 
        open={openInviteDialog} 
        onClose={() => {
          // 
          trackingPrint({params:{
            type:"",
            title:"",
            subtitle:"-",
            extra:{
              timestamp: Date.now(),
              page: "home"
            }
          }});
          setOpenInviteDialog(false);
        }} 
      />}
      
      <AddWidgetButton 
        onSuccess={() => {
          console.log('');
        }}
        onError={(error) => {
          console.error(':', error);
        }}
      />
      {/*  */}
      <AddSecurityWidgetButton 
        onSuccess={() => {
          console.log('');
        }}
        onError={(error) => {
          console.error(':', error);
        }}
      />
      <Box sx={{ mt: 2, mb: 2 }}>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            router.push('/shortcut');
          }}>
          
          {/* <Link href={"/shortcut"}></Link> */}
        </Button>
      </Box>
      <MeetingList maxItemLength={1} />
      
      <Grid2 container spacing={2} sx={{ m: 2 }}>
        <Grid2 size={6}>
          <Button variant="contained" onClick={() => router.push('/intro')}>
            
          </Button>
        </Grid2>
        <Grid2 size={6}>
          <Box sx={{ display: 'flex', justifyContent: 'flex-end' }}>
            <Button
              // fullWidth
              variant="contained"
              onClick={() => {
                router.push('/how-to-use');
              }}>
              
            </Button>
          </Box>
        </Grid2>
      </Grid2>
    </Box>
  )
}
export default function Home() {
  return (
    <AppShell>
      <TabbarContainer titleConf={{ ns: 'emergency-call/pages/home/content', key: 'home.title' }} navBar={<NavBar />}>
        <Content2 />
      </TabbarContainer>
    </AppShell>
  );
}
