import { useRef, useEffect, useState } from 'react';
import { Box } from '@mui/material';
import { fileMapUtils } from '@fanfanlo';



export function FallDownVideoPlayer() {// 

    const fallDownImg = fileMapUtils.getFile('/images/fall-down-video/fall-down-001.jpg')
    console.log('falldown img', fallDownImg)
    const falldownVideo = fileMapUtils.getFile('/video/fall-down/fall-down.mp4')
    console.log('falldownVideo=', falldownVideo)
    const VIDEO_CONFIG = {
      src: falldownVideo, // 
      type: 'video/mp4',
      poster: fallDownImg, // 
      controls: true,
      muted: false,
      loop: true,
    };
  const videoRef = useRef<HTMLVideoElement>(null);
  const [isPlaying, setIsPlaying] = useState(false);

  // 
  useEffect(() => {
    const videoElement = videoRef.current;
    if (!videoElement) return;

    // 
    const initVideo = () => {
      console.log('');
      // 
      videoElement.load();
    };

    // 
    const handleLoadedData = () => {
      console.log('');
    };

    // 
    const handlePlay = () => {
      console.log('');
      setIsPlaying(true);
    };

    const handlePause = () => {
      console.log('');
      setIsPlaying(false);
    };

    // 
    videoElement.addEventListener('loadeddata', handleLoadedData);
    videoElement.addEventListener('play', handlePlay);
    videoElement.addEventListener('pause', handlePause);

    // 
    initVideo();

    // 
    return () => {
      console.log('');
      
      // 
      if (!videoElement.paused) {
        videoElement.pause();
      }
      
      // 
      if (videoElement.src) {
        videoElement.src = '';
      }
      
      // 
      videoElement.removeEventListener('loadeddata', handleLoadedData);
      videoElement.removeEventListener('play', handlePlay);
      videoElement.removeEventListener('pause', handlePause);
      
      // 
      if (videoElement.load) {
        videoElement.load();
      }
    };
  }, []);

  return (
    <Box
      component="div"
      sx={{
        width: '100%',
        height: '100%',
        overflow: 'hidden',
        position: 'relative',
      }}
    >
      <video
        ref={videoRef}
        width="100%"
        height="100%"
        controls={VIDEO_CONFIG.controls}
        muted={VIDEO_CONFIG.muted}
        loop={VIDEO_CONFIG.loop}
        poster={VIDEO_CONFIG.poster}
        style={{
          width: '100%',
          height: '100%',
          objectFit: 'contain',
          backgroundColor: '#000',
          display: 'block',
        }}
      >
        <source src={VIDEO_CONFIG.src} type={VIDEO_CONFIG.type} />
         HTML5 
      </video>
    </Box>
  );
}