import React, { useState, useEffect, useRef } from 'react';
import { Box, TextField, Button, Typography, Paper, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Accordion, AccordionSummary, AccordionDetails } from '@mui/material';
import { ExpandMore as ExpandMoreIcon } from '@mui/icons-material';
import { autoJsStoreUtils, autoWebViewJs } from '@droid/android';
import { PermissionStatusPanel } from '../permission-status';
import { authService } from '@src/services/authService';

export const SensorMonitor: React.FC = () => {
  const [threshold, setThreshold] = useState<string>('5.0');
  const [isMonitoring, setIsMonitoring] = useState<boolean>(false);

  // 
  const [isSensorInfoDialogOpen, setSensorInfoDialogOpen] = useState(false);
  const [isForegroundServiceDialogOpen, setForegroundServiceDialogOpen] = useState(false);

  // useRefre-render
  const sensorCallbackRef = useRef<(passed: boolean) => void>();
  const foregroundServiceCallbackRef = useRef<(passed: boolean) => void>();


  useEffect(() => {
    try {
      const script = `
      var thresholdValue = com.fanfanlo.emergencycall.manager.SensorManager.getThreshold();
      thresholdValue;`;
      const { javaResultString } = autoWebViewJs.callScript(script);
      const thresholdValue = parseFloat(javaResultString || '5.0');
      // console.log("script get threshold=", thresholdValue, javaResultString);
      setThreshold(thresholdValue.toString());
    } catch (error) {
      console.error('Error fetching initial threshold value:', error);
    }
  }, []);

  function readIsMonitoring() {
    try {
      const script = `
      var isMonitoring = com.fanfanlo.emergencycall.manager.SensorManager.isMonitoring();
      isMonitoring;`;
      const { javaResultString } = autoWebViewJs.callScript(script);
      const isMonitoring = javaResultString === "true";
      // console.log("script get isMonitoring=", isMonitoring, javaResultString);
      setIsMonitoring(isMonitoring);
    } catch (error) {
      console.error('Error fetching initial monitoring state:', error);
    }
  }

  useEffect(() => {
    readIsMonitoring();
  }, []);

  const handleSetThreshold = () => {
    const thresholdValue = parseFloat(threshold);
    if (isNaN(thresholdValue)) {
      alert('');
      return;
    }
    try {
      const script = `com.fanfanlo.emergencycall.manager.SensorManager.updateThreshold(${thresholdValue});`;
      autoWebViewJs.callScript(script);
      alert(`: ${thresholdValue}`);
    } catch (error) {
      console.error('Error calling updateThreshold:', error);
      alert('');
    }
  };

  async function handleToggleMonitoring () {
    if (isMonitoring) {
      // 
      try {
        const script = `com.fanfanlo.emergencycall.manager.SensorManager.stopMonitoring();`;
        autoWebViewJs.callScript(script);
        readIsMonitoring();
      } catch (error) {
        console.error('Error stopping monitoring:', error);
        alert('');
      }
    } else {


      const appLoggedIn = await authService.showLoginDialog();
      if(!appLoggedIn){
        alert("")
        return
      }
      // 
      checkShowInfo(passed => {
        if (!passed) return; // 
        try {
          const script = `com.fanfanlo.emergencycall.manager.SensorManager.startMonitoring();`;
          // const script = `com.fanfanlo.emergencycall.manager.SensorManagerShell.startMonitoring();`;
          // const script = `com.fanfanlo.emergencycall.manager.SensorManagerShell.xx();`;
          autoWebViewJs.callScript(script);
          readIsMonitoring();
        } catch (error) {
          console.error('Error starting monitoring:', error);
          alert('');
        }
      });
    }
  };

  useEffect(()=>{
    const id = setInterval(() => {
      readIsMonitoring();
    }, 3000); // 5
    return () => clearInterval(id);
  }, [])
  function checkShowSensorInfo(callback: (passed: boolean) => void) {
    interface Info {
      passed: boolean;
    }
    const info = autoJsStoreUtils.read<Info>('showSensorInfo', { passed: false });
    if (!info || !info.passed) {
      // Dialog
      sensorCallbackRef.current = callback;
      // 
      setSensorInfoDialogOpen(true);
    } else {
      callback(true);
    }
  }

  function checkShowInfo(callback:(passed:boolean)=>void){
    checkShowSensorInfo(passed=>{
      if(!passed) {
        callback(false);
        return;
      };
      checkShowForegroundServiceInfo(callback);
    })
  }

  function checkShowForegroundServiceInfo(callback: (passed: boolean) => void) {
    interface Info {
      passed: boolean;
    }
    const info = autoJsStoreUtils.read<Info>('showForegroundServiceInfo', { passed: false });
    if (!info || !info.passed) {
      // Dialog
      foregroundServiceCallbackRef.current = callback;
      // 
      setForegroundServiceDialogOpen(true);
    } else {
      callback(true);
    }
  }

  // 
  const handleSensorDialog = (passed: boolean) => {
    autoJsStoreUtils.write('showSensorInfo', { passed });
    setSensorInfoDialogOpen(false);
    if (sensorCallbackRef.current) {
      sensorCallbackRef.current(passed);
    }
  };

  // 
  const handleForegroundServiceDialog = (passed: boolean) => {
    autoJsStoreUtils.write('showForegroundServiceInfo', { passed });
    setForegroundServiceDialogOpen(false);
    if (foregroundServiceCallbackRef.current) {
      foregroundServiceCallbackRef.current(passed);
    }
  };


  return (
    <Box>
      {/*  */}
      <Paper elevation={3} sx={{ padding: 2, marginBottom: 2 }}>
        <Typography variant="h6" gutterBottom>
          
        </Typography>
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, marginBottom: 2 }}>
          <TextField
            label=""
            variant="outlined"
            type="number"
            value={threshold}
            onChange={(e) => setThreshold(e.target.value)}
            size="small"
          />
          <Button variant="contained" onClick={handleSetThreshold}>
            
          </Button>
        </Box>
        <Button
          variant="contained"
          color={isMonitoring ? 'error' : 'primary'}
          onClick={handleToggleMonitoring}
          fullWidth
        >
          {isMonitoring ? '' : ''}
        </Button>
      </Paper>

      {/*  */}
      <Accordion >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="permission-panel-content"
          id="permission-panel-header"
        >
          <Typography variant="h6"></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <PermissionStatusPanel />
        </AccordionDetails>
      </Accordion>

      {/*  */}
      <Dialog
        open={isSensorInfoDialogOpen}
        onClose={() => handleSensorDialog(false)}
        aria-labelledby="sensor-dialog-title"
        aria-describedby="sensor-dialog-description"
      >
        <DialogTitle id="sensor-dialog-title">
          {""}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="sensor-dialog-description">
            
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => handleSensorDialog(false)}></Button>
          <Button onClick={() => handleSensorDialog(true)} autoFocus>
            
          </Button>
        </DialogActions>
      </Dialog>

      {/*  */}
      <Dialog
        open={isForegroundServiceDialogOpen}
        onClose={() => handleForegroundServiceDialog(false)}
        aria-labelledby="foreground-dialog-title"
        aria-describedby="foreground-dialog-description"
      >
        <DialogTitle id="foreground-dialog-title">
          {""}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="foreground-dialog-description">
            
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => handleForegroundServiceDialog(false)}></Button>
          <Button onClick={() => handleForegroundServiceDialog(true)} autoFocus>
            
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default SensorMonitor;


