import React, { useState } from 'react';
import {
  Box,
  Typography,
  Switch,
  FormControlLabel,
  Paper,
  Button,
  Divider,
  Alert
} from '@mui/material';
import { VolumeUp, Settings, ExpandLess } from '@mui/icons-material';
import { EmergencyTTSSettings as OriginalEmergencyTTSSettings } from '@src/components/emergency-tts-settings';

// 
// 
const EmergencyTTSSettings: React.FC = () => {
    const [showOriginal, setShowOriginal] = useState(false);

    return (
        <Box>
            {/*  */}
            <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', mb: 2 }}>
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    <VolumeUp />
                    <Typography variant="subtitle2"></Typography>
                </Box>
                
                <Button
                    variant="outlined"
                    size="small"
                    startIcon={showOriginal ? <ExpandLess /> : <Settings />}
                    onClick={() => setShowOriginal(!showOriginal)}
                >
                    {showOriginal ? '' : ''}
                </Button>
            </Box>

            {/*  */}
            {!showOriginal && (
                <Alert severity="info" sx={{ mb: 2 }}>
                    <Typography variant="body2">
                        
                    </Typography>
                </Alert>
            )}

            {/*  */}
            {showOriginal && (
                <Paper 
                    elevation={0} 
                    sx={{ 
                        border: '1px solid', 
                        borderColor: 'divider',
                        borderRadius: 1,
                        overflow: 'hidden'
                    }}
                >
                    <OriginalEmergencyTTSSettings />
                </Paper>
            )}
        </Box>
    );
};

export default EmergencyTTSSettings;