import React, { useState, useEffect, useCallback } from 'react';
import {
  Box,
  Typography,
  Switch,
  TextField,
  Paper,
  Button,
  FormControlLabel,
  Slider,
  Divider,
  Alert,
  Snackbar,
  Collapse,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  IconButton,
  Chip,
  LinearProgress,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Tabs,
  Tab,
  CircularProgress,
} from '@mui/material';
import { 
  PlayArrow, 
  Stop, 
  VolumeUp, 
  Settings, 
  ExpandLess, 
  Mic,
  Save,
  Share,
  Delete,
  Refresh,
  Star,
  StarBorder,
  MusicNote,
  CloudUpload,
  AudioFile,
  InsertDriveFile,
} from '@mui/icons-material';
import { autoJsStoreUtils, autoWebViewJs } from '@droid/android';
import { user } from '@app/model';
import { authService } from '@src/services/authService';
import { ReportButton } from '../report/ReportButton';

// TTS
interface EmergencyTTSSettings {
  enabled: boolean;
  message: string;
  repeatInterval: number;
  voiceSpeed: number;
  voicePitch: number;
  useCustomAudio: boolean;        // 
  medicalEmergencyAudioPath?: string; // 
  lastUpdated: number;
}

// TTS
interface TTSFileInfo {
  fileName: string;
  filePath: string;
  fileSize: number;
  createTime: number;
  type: 'general' | 'medical' | 'security' | 'custom';
  source: 'recorded' | 'uploaded';  // 
  isDefault?: boolean;
  duration?: number;  // 
  format?: string;    // 
  mimeType?: string;  // MIME
}

// 
interface TTSRecordResult {
  success: boolean;
  filePath: string;
  message: string;
}

// 
interface VoiceEmergencyConfig {
  medicalDefault?: string;
  securityDefault?: string;
  lastUpdated: number;
}

// 
interface FileSelectResult {
  success: boolean;
  filePath: string;
  message: string;
  fileInfo?: SelectedFileInfo;
}

interface SelectedFileInfo {
  filePath: string;
  fileName: string;
  fileSize: number;
  mimeType: string;
  duration?: number;
}

interface FileUploadResult {
  success: boolean;
  filePath: string;
  message: string;
  fileInfo?: TTSFileInfo;
}

interface FileValidationResult {
  isValid: boolean;
  message: string;
  fileSize: number;
  duration: number;
  format: string;
  warnings: string[];
}

interface SupportedFormats {
  formats: string[];
  maxFileSizeMB: number;
  maxDurationSeconds: number;
  recommendedDurationSeconds: number;
}

// 
const DEFAULT_SETTINGS: EmergencyTTSSettings = {
  enabled: false,
  message: '',
  repeatInterval: 3000,
  voiceSpeed: 1.0,
  voicePitch: 1.0,
  useCustomAudio: false,
  lastUpdated: Date.now(),
};

const DEFAULT_VOICE_CONFIG: VoiceEmergencyConfig = {
  lastUpdated: Date.now(),
};

const TTS_SETTINGS_KEY = 'emergencyTTSSettings';
const VOICE_CONFIG_KEY = 'voiceEmergencyConfig';

export const EmergencyTTSSettings: React.FC = () => {
  const [settings, setSettings] = useState<EmergencyTTSSettings>(DEFAULT_SETTINGS);
  const [voiceConfig, setVoiceConfig] = useState<VoiceEmergencyConfig>(DEFAULT_VOICE_CONFIG);
  const [fileList, setFileList] = useState<TTSFileInfo[]>([]);
  const [loading, setLoading] = useState(true);
  const [testing, setTesting] = useState(false);
  const [showSuccess, setShowSuccess] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [successMessage, setSuccessMessage] = useState('');
  const [expanded, setExpanded] = useState(false);
  const [activeTab, setActiveTab] = useState(0);
  
  // 
  const [isRecording, setIsRecording] = useState(false);
  const [playingFile, setPlayingFile] = useState<string | null>(null);
  const [recordText, setRecordText] = useState(DEFAULT_SETTINGS.message);
  const [recordSpeed, setRecordSpeed] = useState(1.0);
  const [recordPitch, setRecordPitch] = useState(1.0);
  const [showRecordDialog, setShowRecordDialog] = useState(false);
  const [ttsAvailable, setTtsAvailable] = useState<boolean | null>(null);
  
  // 
  const [isUploading, setIsUploading] = useState(false);
  const [selectedFile, setSelectedFile] = useState<SelectedFileInfo | null>(null);
  const [uploadProgress, setUploadProgress] = useState(0);
  const [supportedFormats, setSupportedFormats] = useState<SupportedFormats | null>(null);

  async function onRecordClick(){
    if(user.data.storeData.user_token){
      setShowRecordDialog(true)
    }else{
      // 

      const appLoggedIn = await authService.showLoginDialog();
      if(appLoggedIn){
        setShowRecordDialog(true);
      }
    }
  }
  // 
  useEffect(() => {
    const loadData = async () => {
      try {
        setLoading(true);
        
        // TTS
        const savedSettings = await autoJsStoreUtils.read<EmergencyTTSSettings>(
          TTS_SETTINGS_KEY, 
          DEFAULT_SETTINGS
        );
        setSettings(savedSettings);
        
        // 
        const savedConfig = await autoJsStoreUtils.read<VoiceEmergencyConfig>(
          VOICE_CONFIG_KEY,
          DEFAULT_VOICE_CONFIG
        );
        setVoiceConfig(savedConfig);
        
        // TTS
        checkTTSAvailability();
        
        // 
        loadFileList();
      } catch (err) {
        console.error(':', err);
        setError('');
      } finally {
        setLoading(false);
      }
    };

    loadData();
  }, []);

  // 
  useEffect(() => {
    loadSupportedFormats();
  }, []);

  // 
  const loadSupportedFormats = useCallback(() => {
    try {
      const script = `var data = com.fanfanlo.emergencycall.manager.TTSRecorderJSInterface.getSupportedAudioFormats();
data;`;
      const { javaResultString } = autoWebViewJs.callScript(script);
      const formats = JSON.parse(javaResultString || '{}') as SupportedFormats;
      setSupportedFormats(formats);
    } catch (error) {
      console.error(':', error);
      setSupportedFormats({
        formats: ['mp3', 'wav', 'm4a', 'aac'],
        maxFileSizeMB: 10,
        maxDurationSeconds: 60,
        recommendedDurationSeconds: 30
      });
    }
  }, []);

  // 
  const loadFileList = useCallback(() => {
    try {
      const script = `var data = com.fanfanlo.emergencycall.manager.TTSRecorderJSInterface.getFileList();
data;`;
      const { javaResultString: listJson } = autoWebViewJs.callScript(script);
      const files = JSON.parse((listJson || "[]") as string) as TTSFileInfo[];
      
      // 
      const filesWithType = files.map(file => ({
        ...file,
        type: (file as any).type || 'general' as const,
        isDefault: (file as any).isDefault || false,
      }));
      
      setFileList(filesWithType);
    } catch (error) {
      console.error(':', error);
      setFileList([]);
    }
  }, []);

  // 
  const handleSelectAndUploadAudioFile = useCallback(async () => {
    if (isUploading) return;
    
    try {
      setIsUploading(true);
      setUploadProgress(0);
      
      const script = `var data = com.fanfanlo.emergencycall.manager.TTSRecorderJSInterface.selectAndUploadAudioFile();
data;`;
      
      // 
      const progressInterval = setInterval(() => {
        setUploadProgress(prev => {
          if (prev >= 90) {
            clearInterval(progressInterval);
            return 90;
          }
          return prev + 10;
        });
      }, 300);
      
      const { javaResultString } = autoWebViewJs.callScript(script);
      const result = JSON.parse(javaResultString || '{}') as FileUploadResult;
      
      clearInterval(progressInterval);
      setUploadProgress(100);
      
      if (result.success) {
        setSuccessMessage('');
        setShowSuccess(true);
        loadFileList(); // 
      } else {
        setError(result.message || '');
      }
    } catch (error) {
      console.error(':', error);
      setError('' + (error as Error).message);
    } finally {
      setIsUploading(false);
      setUploadProgress(0);
    }
  }, [isUploading, loadFileList]);

  // TTS
  const checkTTSAvailability = useCallback(() => {
    try {
      const script = `var data = com.fanfanlo.emergencycall.manager.TTSRecorderJSInterface.isTTSAvailable();
data;`;
      const { javaResultString } = autoWebViewJs.callScript(script);
      setTtsAvailable(javaResultString === 'true');
    } catch (error) {
      console.error('TTS:', error);
      setTtsAvailable(false);
    }
  }, []);

  // 
  const saveSettings = async (newSettings: EmergencyTTSSettings) => {
    try {
      const settingsToSave = {
        ...newSettings,
        lastUpdated: Date.now(),
      };
      
      await autoJsStoreUtils.write(TTS_SETTINGS_KEY, settingsToSave);
      setSettings(settingsToSave);
      setSuccessMessage('');
      setShowSuccess(true);
      setError(null);
    } catch (err) {
      console.error('TTS:', err);
      setError('');
    }
  };

  // 
  const saveVoiceConfig = async (newConfig: VoiceEmergencyConfig) => {
    try {
      const configToSave = {
        ...newConfig,
        lastUpdated: Date.now(),
      };
      
      await autoJsStoreUtils.write(VOICE_CONFIG_KEY, configToSave);
      setVoiceConfig(configToSave);
    } catch (err) {
      console.error(':', err);
      setError('');
    }
  };

  // TTS
  const handleRecord = useCallback(() => {
    if (!recordText.trim()) {
      setError('');
      return;
    }

    if (!ttsAvailable) {
      setError('TTS');
      return;
    }

    try {
      setIsRecording(true);
      // TTS_YYYYMMDD_HHMMSS.wav
      const now = new Date();
      const timestamp = now.getFullYear().toString() + 
                       (now.getMonth() + 1).toString().padStart(2, '0') + 
                       now.getDate().toString().padStart(2, '0') + '_' +
                       now.getHours().toString().padStart(2, '0') + 
                       now.getMinutes().toString().padStart(2, '0') + 
                       now.getSeconds().toString().padStart(2, '0');
      const fileName = `TTS_${timestamp}.wav`;
      
      const script = `var data = com.fanfanlo.emergencycall.manager.TTSRecorderJSInterface.recordTTS("${recordText.replace(/"/g, '\\"')}", "${fileName}", ${recordSpeed}, ${recordPitch});
data;`;
      
      // 
      setTimeout(() => {
        try {
          const { javaResultString: resultJson } = autoWebViewJs.callScript(script);
          console.log(':', resultJson);
          const result = JSON.parse(resultJson || '{}') as TTSRecordResult;
          
          if (result.success) {
            setSuccessMessage('');
            setShowSuccess(true);
            setShowRecordDialog(false);
            loadFileList(); // 
          } else {
            setError(result.message || '');
          }
        } catch (error) {
          console.error(':', error);
          setError('' + (error as Error).message);
        } finally {
          setIsRecording(false);
        }
      }, 2000);
    } catch (error) {
      setError('' + (error as Error).message);
      setIsRecording(false);
    }
  }, [recordText, recordSpeed, recordPitch, ttsAvailable, loadFileList]);

  // 
  const handlePlayFile = useCallback((filePath: string) => {
    try {
      if (playingFile === filePath) {
        setPlayingFile(null);
        return;
      }

      const script = `var data = com.fanfanlo.emergencycall.manager.TTSRecorderJSInterface.playFile("${filePath.replace(/"/g, '\\"')}");
data;`;
      const { javaResultString: success } = autoWebViewJs.callScript(script);
      if (success === 'true') {
        setPlayingFile(filePath);
        setTimeout(() => setPlayingFile(null), 3000);
      } else {
        setError('');
      }
    } catch (error) {
      setError('' + (error as Error).message);
    }
  }, [playingFile]);

  // 
  const handleShareFile = useCallback((filePath: string) => {
    try {
      const script = `var data = com.fanfanlo.emergencycall.manager.TTSRecorderJSInterface.shareFile("${filePath.replace(/"/g, '\\"')}", "");
data;`;
      const { javaResultString: success } = autoWebViewJs.callScript(script);
      if (success !== 'true') {
        setError('');
      }
    } catch (error) {
      setError('' + (error as Error).message);
    }
  }, []);

  // 
  const handleDeleteFile = useCallback((filePath: string) => {
    if (window.confirm('')) {
      try {
        const script = `var data = com.fanfanlo.emergencycall.manager.TTSRecorderJSInterface.deleteFile("${filePath.replace(/"/g, '\\"')}");
data;`;
        const { javaResultString: success } = autoWebViewJs.callScript(script);
        if (success === 'true') {
          setSuccessMessage('');
          setShowSuccess(true);
          loadFileList();
        } else {
          setError('');
        }
      } catch (error) {
        setError('' + (error as Error).message);
      }
    }
  }, [loadFileList]);

  // 
  const setAsDefaultAudio = useCallback(async (filePath: string, type: 'medical' | 'security') => {
    try {
      // Android
      const script = `var data = com.fanfanlo.emergencycall.manager.TTSRecorderJSInterface.setFileAsDefault("${filePath.replace(/"/g, '\\"')}", "${type}");
data;`;
      const { javaResultString: success } = autoWebViewJs.callScript(script);
      
      if (success !== 'true') {
        setError('');
        return;
      }
      
      const newConfig = { ...voiceConfig };
      if (type === 'medical') {
        newConfig.medicalDefault = filePath;
        // TTS
        const updatedSettings = {
          ...settings,
          medicalEmergencyAudioPath: filePath,
          useCustomAudio: true,
        };
        setSettings(updatedSettings);
        await saveSettings(updatedSettings);
      } else {
        newConfig.securityDefault = filePath;
      }
      
      await saveVoiceConfig(newConfig);
      
      // 
      const updatedFiles = fileList.map(file => ({
        ...file,
        isDefault: file.filePath === filePath,
      }));
      setFileList(updatedFiles);
      
      setSuccessMessage(`${type === 'medical' ? '' : ''}`);
      setShowSuccess(true);
    } catch (err) {
      console.error(':', err);
      setError('');
    }
  }, [voiceConfig, fileList, settings, saveSettings, saveVoiceConfig]);

  // 
  const formatFileSize = (bytes: number): string => {
    if (bytes < 1024) return `${bytes} B`;
    if (bytes < 1024 * 1024) return `${(bytes / 1024).toFixed(1)} KB`;
    return `${(bytes / (1024 * 1024)).toFixed(1)} MB`;
  };

  // 
  const formatTime = (timestamp: number): string => {
    return new Date(timestamp).toLocaleString();
  };

  // 
  const formatDuration = (milliseconds: number): string => {
    if (!milliseconds || milliseconds <= 0) return '';
    const seconds = Math.floor(milliseconds / 1000);
    const minutes = Math.floor(seconds / 60);
    const remainingSeconds = seconds % 60;
    if (minutes > 0) {
      return `${minutes}:${remainingSeconds.toString().padStart(2, '0')}`;
    }
    return `${seconds}s`;
  };

  // TTS
  const testTTS = async () => {
    if (!settings.message.trim()) {
      setError('');
      return;
    }

    try {
      setTesting(true);
      
      // AndroidTTS
      const script = `
        com.fanfanlo.emergencycall.manager.EmergencyTTSManager.testTTS("${settings.message}", ${settings.voiceSpeed}, ${settings.voicePitch});
      `;
      
      await new Promise(resolve => setTimeout(resolve, 500)); // 
      // TODO: 
      autoWebViewJs.callScript(script);
      
    } catch (err) {
      console.error('TTS:', err);
      setError('TTS');
    } finally {
      setTesting(false);
    }
  };

  // TTS
  const stopTest = async () => {
    try {
      const script = `
        com.fanfanlo.emergencycall.manager.EmergencyTTSManager.stopTest();
      `;
      // TODO: 
      autoWebViewJs.callScript(script);
      setTesting(false);
    } catch (err) {
      console.error('TTS:', err);
    }
  };

  const handleSettingChange = (key: keyof EmergencyTTSSettings, value: any) => {
    const newSettings = { ...settings, [key]: value };
    setSettings(newSettings);
    
    // /
    if (key === 'enabled') {
      saveSettings(newSettings);
    }
  };

  const handleSave = () => {
    if (settings.enabled && !settings.useCustomAudio && !settings.message.trim()) {
      setError('TTS');
      return;
    }
    saveSettings(settings);
  };

  if (loading) {
    return (
      <Box sx={{ p: 2, textAlign: 'center' }}>
        <Typography>...</Typography>
      </Box>
    );
  }

  return (
    <Box sx={{ mb: 3 }}>
      {/*  */}
      <Paper 
        elevation={1} 
        sx={{ 
          p: 2, 
          display: 'flex', 
          alignItems: 'center',
          justifyContent: 'space-between',
          borderRadius: 1,
        }}
      >
        <Box sx={{ 
          display: 'flex', 
          alignItems: 'center', 
          gap: { xs: 1, sm: 2 },
          flexWrap: { xs: 'wrap', sm: 'nowrap' },
          flex: 1,
          minWidth: 0
        }}>
          <Typography 
            variant="subtitle1" 
            sx={{ 
              display: 'flex', 
              alignItems: 'center', 
              gap: 1,
              fontSize: { xs: '0.95rem', sm: '1.25rem' },
              minWidth: 0,
              flexShrink: 0
            }}
          >
            <VolumeUp fontSize="small" />
            <Box component="span" sx={{ 
              display: { xs: 'none', sm: 'inline' }
            }}></Box>
            <Box component="span" sx={{ 
              display: { xs: 'inline', sm: 'none' }
            }}></Box>
          </Typography>
          
          <Box sx={{ 
            display: 'flex', 
            alignItems: 'center', 
            gap: { xs: 1, sm: 2 },
            flexWrap: { xs: 'wrap', sm: 'nowrap' }
          }}>
            <FormControlLabel
              control={
                <Switch
                  checked={settings.enabled}
                  onChange={(e) => handleSettingChange('enabled', e.target.checked)}
                  color="primary"
                  size="small"
                />
              }
              label={settings.enabled ? '' : ''}
              sx={{ 
                margin: 0,
                '& .MuiFormControlLabel-label': {
                  fontSize: { xs: '0.8rem', sm: '0.875rem' }
                }
              }}
            />
            
            {/*  */}
            {/* <Chip 
              size="small"
              icon={ttsAvailable ? <VolumeUp /> : <MusicNote />}
              label={ttsAvailable === null ? '...' : (ttsAvailable ? 'TTS' : 'TTS')}
              color={ttsAvailable === null ? 'default' : (ttsAvailable ? 'success' : 'warning')}
              variant="outlined"
              sx={{ 
                fontSize: { xs: '0.7rem', sm: '0.8125rem' },
                height: { xs: '24px', sm: '32px' }
              }}
            /> */}
          </Box>
        </Box>
        
        <Button
          variant="outlined"
          size="small"
          startIcon={expanded ? <ExpandLess /> : <Settings />}
          onClick={() => setExpanded(!expanded)}
          sx={{ 
            fontSize: { xs: '0.75rem', sm: '0.875rem' },
            padding: { xs: '4px 8px', sm: '6px 16px' },
            minWidth: { xs: 'auto', sm: '64px' },
            flexShrink: 0
          }}
        >
          <Box component="span" sx={{ display: { xs: 'none', sm: 'inline' } }}>
            {expanded ? '' : ''}
          </Box>
          <Box component="span" sx={{ display: { xs: 'inline', sm: 'none' } }}>
            {expanded ? '' : ''}
          </Box>
        </Button>
      </Paper>

      {/*  */}
      <Collapse in={expanded}>
        <Paper 
          elevation={0} 
          sx={{ 
            mt: 1, 
            p: 0,
            border: '1px solid', 
            borderColor: 'divider',
            borderRadius: 1,
          }}
        >
          {/*  */}
          <Tabs 
            value={activeTab} 
            onChange={(_, newValue) => setActiveTab(newValue)}
            variant="fullWidth"
          >
            {/* <Tab label="" /> */}
            <Tab label="/" />
            <Tab label="" />
          </Tabs>

          {/*  */}
          <Box sx={{ p: 2 }}>
            {/*  Tab */}
            {activeTab === -1 && (
              <Box>
                {/*  */}
                <Box sx={{ mb: 3 }}>
                  <Typography variant="subtitle2" fontWeight="bold" sx={{ mb: 2 }}>
                    
                  </Typography>
                  <TextField
                    fullWidth
                    multiline
                    rows={3}
                    value={settings.message}
                    onChange={(e) => handleSettingChange('message', e.target.value)}
                    placeholder="..."
                    disabled={!settings.enabled}
                    helperText={`: ${settings.message.length} `}
                    sx={{ 
                      '& .MuiInputBase-input': {
                        fontSize: '16px',
                        lineHeight: 1.5,
                      }
                    }}
                  />
                </Box>

                {/*  */}
                {settings.enabled && (
                  <>
                    <Divider sx={{ my: 3 }} />
                    
                    <Typography variant="subtitle2" fontWeight="bold" sx={{ mb: 2 }}>
                      
                    </Typography>

                    {/*  */}
                    <Box sx={{ mb: 3 }}>
                      <Typography variant="body2" sx={{ mb: 1 }}>
                        : {settings.repeatInterval / 1000} 
                      </Typography>
                      <Slider
                        value={settings.repeatInterval}
                        onChange={(_, value) => handleSettingChange('repeatInterval', value)}
                        min={2000}
                        max={10000}
                        step={500}
                        marks={[
                          { value: 2000, label: '2s' },
                          { value: 5000, label: '5s' },
                          { value: 10000, label: '10s' },
                        ]}
                        valueLabelDisplay="auto"
                        valueLabelFormat={(value) => `${value / 1000}s`}
                      />
                    </Box>

                    {/*  */}
                    <Box sx={{ mb: 3 }}>
                      <Typography variant="body2" sx={{ mb: 1 }}>
                        : {settings.voiceSpeed}x
                      </Typography>
                      <Slider
                        value={settings.voiceSpeed}
                        onChange={(_, value) => handleSettingChange('voiceSpeed', value)}
                        min={0.5}
                        max={2.0}
                        step={0.1}
                        marks={[
                          { value: 0.5, label: '0.5x' },
                          { value: 1.0, label: '1.0x' },
                          { value: 2.0, label: '2.0x' },
                        ]}
                        valueLabelDisplay="auto"
                      />
                    </Box>

                    {/*  */}
                    <Box sx={{ mb: 3 }}>
                      <Typography variant="body2" sx={{ mb: 1 }}>
                        : {settings.voicePitch}x
                      </Typography>
                      <Slider
                        value={settings.voicePitch}
                        onChange={(_, value) => handleSettingChange('voicePitch', value)}
                        min={0.5}
                        max={2.0}
                        step={0.1}
                        marks={[
                          { value: 0.5, label: '' },
                          { value: 1.0, label: '' },
                          { value: 2.0, label: '' },
                        ]}
                        valueLabelDisplay="auto"
                      />
                    </Box>
                  </>
                )}
              </Box>
            )}

            {/*  Tab */}
            {activeTab === 0 && (
              <Box>
                {/*  */}
                <Box sx={{ mb: 4 }}>
                  <Typography variant="subtitle2" fontWeight="bold" sx={{ mb: 2 }}>
                    
                  </Typography>
                  
                  {!ttsAvailable ? (
                    <Alert severity="warning" sx={{ mb: 2 }}>
                      TTS
                    </Alert>
                  ) : (
                    <Button
                      variant="contained"
                      startIcon={<Mic />}
                      onClick={onRecordClick}
                      disabled={isUploading}
                      sx={{ mb: 3 }}
                    >
                      
                    </Button>
                  )}
                  
                  <Typography variant="body2" color="text.secondary">
                     ""<br />
                     <br />
                     <br />
                     
                  </Typography>
                </Box>

                <Divider sx={{ my: 3 }} />

                {/*  */}
                <Box>
                  <Typography variant="subtitle2" fontWeight="bold" sx={{ mb: 2 }}>
                    
                  </Typography>
                  
                  <Button
                    variant="outlined"
                    startIcon={isUploading ? <CircularProgress size={16} /> : <CloudUpload />}
                    onClick={handleSelectAndUploadAudioFile}
                    disabled={isUploading}
                    sx={{ mb: 2 }}
                  >
                    {isUploading ? '...' : ''}
                  </Button>
                  
                  {/*  */}
                  {isUploading && uploadProgress > 0 && (
                    <Box sx={{ mb: 2 }}>
                      <LinearProgress 
                        variant="determinate" 
                        value={uploadProgress} 
                        sx={{ mb: 1 }}
                      />
                      <Typography variant="body2" color="text.secondary" sx={{ textAlign: 'center' }}>
                        ... {uploadProgress}%
                      </Typography>
                    </Box>
                  )}
                  
                  {supportedFormats && supportedFormats.formats && (
                    <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                      : {supportedFormats.formats.join(', ').toUpperCase()}<br />
                      : {supportedFormats.maxFileSizeMB}MB<br />
                      : {supportedFormats.recommendedDurationSeconds}
                    </Typography>
                  )}
                  
                  <Typography variant="body2" color="text.secondary">
                     <br />
                    {/*  <br />
                     <br /> */}
                     
                  </Typography>
                </Box>
              </Box>
            )}

            {/*  Tab */}
            {activeTab === 1 && (
              <Box>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                  <Typography variant="subtitle2" fontWeight="bold">
                    
                  </Typography>
                  <Button
                    size="small"
                    startIcon={<Refresh />}
                    onClick={loadFileList}
                  >
                    
                  </Button>
                </Box>

                {fileList.length === 0 ? (
                  <Typography color="text.secondary" sx={{ textAlign: 'center', py: 3 }}>
                    
                  </Typography>
                ) : (
                  <List dense>
                    {fileList.map((file, index) => (
                      <React.Fragment key={file.filePath}>
                        <ListItem sx={{ pl: 0 }}>
                          <ListItemText
                            primary={
                              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, flexWrap: 'wrap' }}>
                                <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                                  {file.source === 'uploaded' ? <InsertDriveFile fontSize="small" /> : <Mic fontSize="small" />}
                                  <Typography variant="body2" fontWeight="medium">
                                    {file.fileName}
                                  </Typography>
                                </Box>
                                
                                {/*  */}
                                <Box sx={{ display: 'flex', gap: 0.5 }}>
                                  {file.isDefault && (
                                    <Chip size="small" label="" color="primary" />
                                  )}
                                  <Chip 
                                    size="small" 
                                    label={file.source === 'uploaded' ? '' : ''} 
                                    color={file.source === 'uploaded' ? 'secondary' : 'default'}
                                    variant="outlined"
                                  />
                                  {file.format && (
                                    <Chip 
                                      size="small" 
                                      label={file.format.toUpperCase()} 
                                      color="default" 
                                      variant="outlined"
                                    />
                                  )}
                                </Box>
                              </Box>
                            }
                            secondary={
                              <Box>
                                <Typography variant="caption" color="text.secondary">
                                  {formatFileSize(file.fileSize)}  {formatTime(file.createTime)}
                                  {file.duration && file.duration > 0 && `  ${formatDuration(file.duration)}`}
                                </Typography>
                                {file.mimeType && (
                                  <Typography variant="caption" color="text.secondary" sx={{ display: 'block' }}>
                                    {file.mimeType}
                                  </Typography>
                                )}
                              </Box>
                            }
                          />
                          <ListItemSecondaryAction>
                            <Box sx={{ display: 'flex', gap: 0.5 }}>
                              <IconButton
                                size="small"
                                onClick={() => handlePlayFile(file.filePath)}
                                color={playingFile === file.filePath ? "primary" : "default"}
                              >
                                {playingFile === file.filePath ? <Stop /> : <PlayArrow />}
                              </IconButton>
                              <IconButton
                                size="small"
                                onClick={() => setAsDefaultAudio(file.filePath, 'medical')}
                                color={file.isDefault ? "primary" : "default"}
                              >
                                {file.isDefault ? <Star /> : <StarBorder />}
                              </IconButton>
                              <IconButton
                                size="small"
                                onClick={() => handleShareFile(file.filePath)}
                              >
                                <Share />
                              </IconButton>
                              <IconButton
                                size="small"
                                onClick={() => handleDeleteFile(file.filePath)}
                                color="error"
                              >
                                <Delete />
                              </IconButton>
                            </Box>
                          </ListItemSecondaryAction>
                        </ListItem>
                        {index < fileList.length - 1 && <Divider />}
                      </React.Fragment>
                    ))}
                  </List>
                )}
              </Box>
            )}

            {/*  */}
            <Divider sx={{ my: 3 }} />
            
            {/* <Box sx={{ display: 'flex', gap: 2, flexWrap: 'wrap', mb: 2 }}>
              <Button
                variant="contained"
                onClick={handleSave}
                sx={{ minWidth: 100 }}
              >
                
              </Button>

              {settings.enabled && settings.message.trim() && (
                <>
                  {!testing ? (
                    <Button
                      variant="outlined"
                      startIcon={<PlayArrow />}
                      onClick={testTTS}
                      sx={{ minWidth: 100 }}
                    >
                      
                    </Button>
                  ) : (
                    <Button
                      variant="outlined"
                      color="error"
                      startIcon={<Stop />}
                      onClick={stopTest}
                      sx={{ minWidth: 100 }}
                    >
                      
                    </Button>
                  )}
                </>
              )}
            </Box> */}

            {/*  */}
            <Box sx={{ p: 2, backgroundColor: 'rgba(0,0,0,0.05)', borderRadius: 1 }}>
              <Typography variant="body2" color="text.secondary">
                <strong>:</strong><br />
                 1: <br />
                 2: <br />
                {/*  3: <br /> */}
                 3: <br />
                <br />
                <strong>:</strong><br />
                 <br />
                 <br />
                 <br />
                 
              </Typography>
            </Box>
          </Box>
        </Paper>
      </Collapse>

      {/*  */}
      <Dialog open={showRecordDialog} onClose={() => setShowRecordDialog(false)} maxWidth="md" fullWidth>
        <DialogTitle></DialogTitle>
        <DialogContent>
          <TextField
            fullWidth
            multiline
            rows={4}
            label=""
            value={recordText}
            onChange={(e) => setRecordText(e.target.value)}
            margin="normal"
            placeholder="..."
            helperText={`: ${recordText.length} `}
          />
          
          {isRecording && (
            <Box sx={{ mt: 2 }}>
              <LinearProgress />
              <Typography variant="body2" sx={{ mt: 1, textAlign: 'center' }}>
                ...
              </Typography>
            </Box>
          )}
          
          <Box sx={{ mt: 3 }}>
            <Typography variant="subtitle2" gutterBottom>
              
            </Typography>
            
            <Box sx={{ mt: 2 }}>
              <Typography variant="body2" gutterBottom>
                : {recordSpeed}x
              </Typography>
              <Slider
                value={recordSpeed}
                onChange={(_, value) => setRecordSpeed(value as number)}
                min={0.5}
                max={2.0}
                step={0.1}
                marks={[
                  { value: 0.5, label: '' },
                  { value: 1.0, label: '' },
                  { value: 2.0, label: '' },
                ]}
                disabled={isRecording}
              />
            </Box>
            
            <Box sx={{ mt: 2 }}>
              <Typography variant="body2" gutterBottom>
                : {recordPitch}x
              </Typography>
              <Slider
                value={recordPitch}
                onChange={(_, value) => setRecordPitch(value as number)}
                min={0.5}
                max={2.0}
                step={0.1}
                marks={[
                  { value: 0.5, label: '' },
                  { value: 1.0, label: '' },
                  { value: 2.0, label: '' },
                ]}
                disabled={isRecording}
              />
            </Box>
          </Box>
        </DialogContent>
        <DialogActions>
          <ReportButton />
          <Button 
            onClick={() => setShowRecordDialog(false)}
            disabled={isRecording}
          >
            
          </Button>
          <Button 
            onClick={handleRecord} 
            variant="contained"
            disabled={!recordText.trim() || isRecording}
            startIcon={isRecording ? undefined : <Mic />}
          >
            {isRecording ? '...' : ''}
          </Button>
        </DialogActions>
      </Dialog>

      {/*  */}
      <Snackbar
        open={showSuccess}
        autoHideDuration={3000}
        onClose={() => setShowSuccess(false)}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert onClose={() => setShowSuccess(false)} severity="success">
          {successMessage || 'TTS'}
        </Alert>
      </Snackbar>

      {/*  */}
      <Snackbar
        open={!!error}
        autoHideDuration={6000}
        onClose={() => setError(null)}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert onClose={() => setError(null)} severity="error">
          {error}
        </Alert>
      </Snackbar>
    </Box>
  );
};