import React, { useState, useEffect } from 'react';
import { Button, Alert, Box, Typography, Dialog, DialogTitle, DialogContent, DialogActions } from '@mui/material';
import AddToHomeScreenIcon from '@mui/icons-material/AddToHomeScreen';
import SettingsIcon from '@mui/icons-material/Settings';
import { autoWebViewJs } from '@droid/android';


interface AddWidgetButtonProps {
  onSuccess?: () => void;
  onError?: (error: string) => void;
}

const AddWidgetButton: React.FC<AddWidgetButtonProps> = ({ 
  onSuccess, 
  onError 
}) => {
  const [isAdding, setIsAdding] = useState(false);
  const [message, setMessage] = useState<string>('');
  const [messageType, setMessageType] = useState<'success' | 'error' | 'info'>('info');
  const [deviceManufacturer, setDeviceManufacturer] = useState<string>('');
  const [showPermissionGuide, setShowPermissionGuide] = useState(false);

  // 
  useEffect(() => {
    try {
      const manufacturerScript = `
var manufacturer = com.fanfanlo.emergencycall.utils.WidgetUtils.getDeviceManufacturerName();
manufacturer;`;
      
      const { javaResultString } = autoWebViewJs.callScript(manufacturerScript);
      setDeviceManufacturer(javaResultString || '');
    } catch (error) {
      console.warn(':', error);
      setDeviceManufacturer('');
    }
  }, []);

  const handleOpenSettings = async () => {
    try {
      console.log('VERSION_AWARE_WIDGET');
      
      const settingsScript = `
var success = com.fanfanlo.emergencycall.utils.WidgetUtils.openPermissionSettings();
success;`;
      
      const { javaResultString } = autoWebViewJs.callScript(settingsScript);
      const success = javaResultString === "true";
      
      if (success) {
        console.log('VERSION_AWARE_WIDGET');
      } else {
        console.warn('VERSION_AWARE_WIDGET');
      }
    } catch (error) {
      console.error('VERSION_AWARE_WIDGET:', error);
    }
  };

  const executeAddWidget = async () => {
    try {
      setMessage('...');
      setMessageType('info');
      
      const addScript = `
var success = com.fanfanlo.emergencycall.utils.WidgetUtils.requestAddMedicalWidget();
success;`;
      
      const { javaResultString: addResult } = autoWebViewJs.callScript(addScript);
      const addSuccess = addResult === "true";
      
      if (addSuccess) {
        setMessage('');
        setMessageType('success');
        onSuccess?.();
      } else {
        throw new Error('');
      }
    } catch (error) {
      const errorMsg = error instanceof Error ? error.message : '';
      setMessage(errorMsg);
      setMessageType('error');
      onError?.(errorMsg);
    }
  };

  const handleAddWidget = async () => {
    try {
      setIsAdding(true);
      setMessage('...');
      setMessageType('info');

      // 
      const permissionScript = `
var permissionInfo = com.fanfanlo.emergencycall.utils.WidgetUtils.getDevicePermissionInfo();
permissionInfo;`;
      
      const { javaResultString: permissionResult } = autoWebViewJs.callScript(permissionScript);
      const permissionInfo = JSON.parse(permissionResult || '{}');
      
      if (permissionInfo.needsPermission) {
        // 
//         const stepsScript = `
// var steps = com.fanfanlo.emergencycall.utils.WidgetUtils.getManualAddSteps();
// steps;`;
        
//         const { javaResultString: stepsResult } = autoWebViewJs.callScript(stepsScript);
        
        setMessage(` "" `);
        setMessageType('info');
        setShowPermissionGuide(true);
      } else {
        // 
        await executeAddWidget();
      }
      
    } catch (error) {
      const errorMsg = error instanceof Error ? error.message : '';
      setMessage(errorMsg);
      setMessageType('error');
      onError?.(errorMsg);
    } finally {
      setIsAdding(false);
    }
  };

  return (
    <Box sx={{ my: 2 }}>
      <Button
        variant="outlined"
        color="primary"
        size="large"
        fullWidth
        // startIcon={<AddToHomeScreenIcon />}
        onClick={handleAddWidget}
        disabled={isAdding}
        sx={{
          py: 1.5,
          borderColor: '#DC2626',
          color: '#DC2626',
          '&:hover': {
            borderColor: '#B91C1C',
            backgroundColor: 'rgba(220, 38, 38, 0.04)',
          },
          '&.Mui-disabled': {
            opacity: 0.6,
          },
        }}
      >
        {isAdding ? '...' : ' '}
      </Button>
      
      {message && (
        <Alert 
          severity={messageType} 
          sx={{ mt: 1 }}
          onClose={() => setMessage('')}
        >
          <Typography variant="body2" style={{ whiteSpace: 'pre-line' }}>
            {message}
          </Typography>
        </Alert>
      )}
      
      {/*  */}
      <Dialog 
        open={showPermissionGuide}
        onClose={() => setShowPermissionGuide(false)}
        fullWidth
        maxWidth="sm"
      >
        <DialogTitle>
          
        </DialogTitle>
        <DialogContent>
          <Typography variant="body1" sx={{ mb: 2 }}>
             {deviceManufacturer}
          </Typography>
        </DialogContent>
        <DialogActions sx={{ px: 3, pb: 2 }}>
          <Button
            onClick={async () => {
              setShowPermissionGuide(false);
              // 
              await executeAddWidget();
            }}
            color="inherit"
          >
            
          </Button>
          <Button
            variant="contained"
            startIcon={<SettingsIcon />}
            onClick={handleOpenSettings}
            sx={{
              backgroundColor: '#DC2626',
              '&:hover': {
                backgroundColor: '#B91C1C',
              },
            }}
          >
            
          </Button>
        </DialogActions>
      </Dialog>
      
      {/* {!message && !showPermissionGuide && deviceManufacturer && (
        <Typography 
          variant="caption" 
          color="text.secondary" 
          sx={{ display: 'block', mt: 1, textAlign: 'center' }}
        >
          {deviceManufacturer}  
        </Typography>
      )} */}
    </Box>
  );
};

export default AddWidgetButton;