import React, { useState, useEffect, useRef } from 'react';
import { 
    Box, 
    TextField, 
    Typography, 
    Paper, 
    Accordion, 
    AccordionSummary, 
    AccordionDetails,
    Divider
} from '@mui/material';
import { ExpandMore as ExpandMoreIcon } from '@mui/icons-material';
import { autoWebViewJs } from '@droid/android';
import { PermissionStatusPanel } from './components/PermissionStatusPanel';
import { PreAlarmConfig, EmergencyTTSSettings } from './components';
import toast from 'react-hot-toast';
import VoiceEmergencySettings, { VoiceEmergencySettingsRef } from './components/VoiceEmergencySettings';

interface FallDetectionSettingsProps {
    voiceSettingsRef?: React.RefObject<VoiceEmergencySettingsRef>;
}
// import { VoiceEmergencySettings } from '@src/components/voice-emergency-settings';

export const FallDetectionSettings: React.FC<FallDetectionSettingsProps> = ({ voiceSettingsRef }) => {
    const [threshold, setThreshold] = useState<string>('5.0');
    const [lastValidThreshold, setLastValidThreshold] = useState<string>('5.0');
    const debounceTimerRef = useRef<NodeJS.Timeout | null>(null);
    const localVoiceSettingsRef = useRef<VoiceEmergencySettingsRef>(null);
    const effectiveVoiceSettingsRef = voiceSettingsRef || localVoiceSettingsRef;
    
    // 
    const [permissionExpanded, setPermissionExpanded] = useState(false);
    const [permissionStats, setPermissionStats] = useState({ granted: 0, denied: 0, unknown: 0 });

    // Android
    const fetchThresholdFromAndroid = async (): Promise<string> => {
        try {
            const script = `
            var thresholdValue = com.fanfanlo.emergencycall.manager.SensorManager.getThreshold();
            thresholdValue;`;
            const { javaResultString } = autoWebViewJs.callScript(script);
            const thresholdValue = parseFloat(javaResultString || '5.0');
            return thresholdValue.toString();
        } catch (error) {
            console.error('Error fetching threshold value:', error);
            return '5.0';
        }
    };

    useEffect(() => {
        const initializeThreshold = async () => {
            const fetchedThreshold = await fetchThresholdFromAndroid();
            setThreshold(fetchedThreshold);
            setLastValidThreshold(fetchedThreshold);
        };
        initializeThreshold();

        // 
        return () => {
            if (debounceTimerRef.current) {
                clearTimeout(debounceTimerRef.current);
            }
        };
    }, []);


    // Android
    const submitThreshold = async (thresholdValue: number): Promise<boolean> => {
        try {
            const script = `com.fanfanlo.emergencycall.manager.SensorManager.updateThreshold(${thresholdValue});`;
            autoWebViewJs.callScript(script);
            console.log(`: ${thresholdValue}`);
            toast.success('');
            return true;
        } catch (error) {
            console.error('Error calling updateThreshold:', error);
            toast.error(':' + error);
            return false;
        }
    };

    // 
    const isValidThreshold = (value: string): boolean => {
        const numValue = parseFloat(value);
        return !isNaN(numValue) && numValue > 0;
    };

    // toast
    const showToast = (message: string) => {
        // alerttoasttoast
        alert(message);
    };

    // onChange
    const handleThresholdChange = (newValue: string) => {
        setThreshold(newValue);
        
        // 
        if (debounceTimerRef.current) {
            clearTimeout(debounceTimerRef.current);
        }
        
        // 
        debounceTimerRef.current = setTimeout(async () => {
            if (isValidThreshold(newValue)) {
                // 
                const numValue = parseFloat(newValue);
                const success = await submitThreshold(numValue);
                if (success) {
                    setLastValidThreshold(newValue);
                } else {
                    showToast('');
                    // 
                    setThreshold(lastValidThreshold);
                }
            } else {
                // 
                showToast('');
                if (lastValidThreshold) {
                    setThreshold(lastValidThreshold);
                } else {
                    // Android
                    const fetchedThreshold = await fetchThresholdFromAndroid();
                    setThreshold(fetchedThreshold);
                    setLastValidThreshold(fetchedThreshold);
                }
            }
        }, 1500); // 1.5
    };




    return (
        <Box sx={{ p: 2 }}>
            {/*  */}
            <Paper elevation={1} sx={{ padding: 2, marginBottom: 2 }}>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, marginBottom: 2 }}>
                    <TextField
                        label=""
                        variant="outlined"
                        type="number"
                        value={threshold}
                        onChange={(e) => handleThresholdChange(e.target.value)}
                        size="small"
                        helperText="3-15"
                    />
                </Box>
            </Paper>

            {/*  */}
            <Accordion 
                sx={{ marginBottom: 2 }}
                expanded={permissionExpanded}
                onChange={(event, isExpanded) => setPermissionExpanded(isExpanded)}
            >
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="permission-panel-content"
                    id="permission-panel-header"
                >
                    <Box sx={{ display: 'flex', flexDirection: 'column', width: '100%' }}>
                        <Typography variant="h6" sx={{ fontWeight: 600 }}>
                            
                        </Typography>
                        
                        {/*  */}
                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mt: 0.5 }}>
                            <Box sx={{ display: 'flex', alignItems: 'center', gap: 0.5 }}>
                                <Box 
                                    sx={{ 
                                        width: 8, 
                                        height: 8, 
                                        borderRadius: '50%', 
                                        backgroundColor: 'success.main' 
                                    }} 
                                />
                                <Typography variant="caption" color="success.main">
                                    /{permissionStats.granted}
                                </Typography>
                            </Box>
                            
                            <Box sx={{ display: 'flex', alignItems: 'center', gap: 0.5 }}>
                                <Box 
                                    sx={{ 
                                        width: 8, 
                                        height: 8, 
                                        borderRadius: '50%', 
                                        backgroundColor: 'error.main' 
                                    }} 
                                />
                                <Typography variant="caption" color="error.main">
                                    /{permissionStats.denied}
                                </Typography>
                            </Box>
                            
                            <Box sx={{ display: 'flex', alignItems: 'center', gap: 0.5 }}>
                                <Box 
                                    sx={{ 
                                        width: 8, 
                                        height: 8, 
                                        borderRadius: '50%', 
                                        backgroundColor: 'warning.main' 
                                    }} 
                                />
                                <Typography variant="caption" color="warning.main">
                                    /{permissionStats.unknown}
                                </Typography>
                            </Box>
                        </Box>
                    </Box>
                </AccordionSummary>
                <AccordionDetails>
                    <PermissionStatusPanel 
                        isExpanded={permissionExpanded}
                        onStatsChange={setPermissionStats}
                    />
                </AccordionDetails>
            </Accordion>

            <Divider sx={{ my: 3 }} />

            {/*  */}
            <Box sx={{ mb: 3 }}>
                {/* <Typography variant="h6" gutterBottom>
                    
                </Typography> */}
                <PreAlarmConfig />
            </Box>

            <Divider sx={{ my: 3 }} />

            {/*  */}
            <Box sx={{ mb: 3 }}>
                <VoiceEmergencySettings ref={effectiveVoiceSettingsRef} />
            </Box>

        </Box>
    );
};

export default FallDetectionSettings;