import React, { useState, useEffect, useRef } from 'react';
import {
    Box,
    Paper,
    Typography,
    IconButton,
    Collapse,
    Switch,
    FormControlLabel,
    useTheme,
    alpha,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
    Button
} from '@mui/material';
import FallRescueAnimation from './components/FallRescueAnimation';
import {
    ExpandLess,
    ExpandMore,
    Animation as AnimationIcon
} from '@mui/icons-material';
import { autoJsStoreUtils, autoWebViewJs } from '@droid/android';
import { authService } from '@src/services/authService';
import FallDetectionSettings from './FallDetectionSettings';
import { VoiceEmergencySettingsRef } from './components/VoiceEmergencySettings';
import { SENSOR_MONITORING_STATE_CHANGED } from './constants';
import { PrivacyDialogDisplay } from '@src/components/privacies/PrivacyDialogDisplay';
import { fileMapUtils, IDispatcher } from '@fanfanlo';
import { privacyDialogClosedEventType } from '@src/components/privacies/PrivacyDialogAuto';
import { FallDownVideoPlayer } from './components/FallDownVideoPlayer';

interface FallDetectionWrapperProps {
    title: string;
    isExpanded: boolean;
    onToggleExpanded: (expanded: boolean) => void;
    animationType?: 'fall' | 'shake';
    dispatcher?:IDispatcher
}
interface IPrivacyInfo {
  agree: boolean;
  disagreeTime: number;
}
interface IWaitPlayVideoInfo{
    isAppFirstOpen:boolean;
}
// 
const REQUIRE_LOGIN_FOR_FALL_DETECTION = true; // 

const FallDetectionWrapper: React.FC<FallDetectionWrapperProps> = ({
    title,
    isExpanded,
    onToggleExpanded,
    animationType,
    dispatcher
}) => {
    const theme = useTheme();
    //  SensorMonitor.tsx - 
    const [fallThreshold, setFallThreshold] = useState<string>('5.0');
    const voiceSettingsRef = useRef<VoiceEmergencySettingsRef>(null);
    
    // VoiceEmergencySettings
    useEffect(() => {
        if (isExpanded && voiceSettingsRef.current) {
            voiceSettingsRef.current.reloadConfig();
        }
    }, [isExpanded]);
    
    const [fallIsMonitoring, setFallIsMonitoring] = useState<boolean>(false);
    const [fallSensorInfoDialogOpen, setFallSensorInfoDialogOpen] = useState(false);
    const [fallForegroundServiceDialogOpen, setFallForegroundServiceDialogOpen] = useState(false);
    
    // 
    const [privacyDialogOpen, setPrivacyDialogOpen] = useState(false);

    //  SensorMonitor.tsx - useRef
    const fallSensorCallbackRef = useRef<(passed: boolean) => void>();
    const fallForegroundServiceCallbackRef = useRef<(passed: boolean) => void>();
    const privacyCallbackRef = useRef<(passed: boolean) => void>();

    //  SensorMonitor.tsx - 
    useEffect(() => {
        try {
            const script = `
            var thresholdValue = com.fanfanlo.emergencycall.manager.SensorManager.getThreshold();
            thresholdValue;`;
            const { javaResultString } = autoWebViewJs.callScript(script);
            const thresholdValue = parseFloat(javaResultString || '5.0');
            setFallThreshold(thresholdValue.toString());
        } catch (error) {
            console.error('Error fetching initial threshold value:', error);
        }
    }, []);

    //  SensorMonitor.tsx - 
    function readFallIsMonitoring() {
        try {
            const script = `
            var isMonitoring = com.fanfanlo.emergencycall.manager.SensorManager.isMonitoring();
            isMonitoring;`;
            const { javaResultString } = autoWebViewJs.callScript(script);
            const isMonitoring = javaResultString === "true";
            setFallIsMonitoring(isMonitoring);
        } catch (error) {
            console.error('Error fetching initial monitoring state:', error);
        }
    }

    //  SensorMonitor.tsx - 
    useEffect(() => {
        readFallIsMonitoring();
        
        // Android
        const handleMonitoringStateChange = (event: any) => {
            const { isMonitoring: newState } = event.detail || {};
            if (typeof newState === 'boolean') {
                setFallIsMonitoring(newState);
            }
        };

        window.addEventListener(SENSOR_MONITORING_STATE_CHANGED, handleMonitoringStateChange);
        return () => window.removeEventListener(SENSOR_MONITORING_STATE_CHANGED, handleMonitoringStateChange);
    }, []);

    //  SensorMonitor.tsx - 
    function checkShowFallSensorInfo(callback: (passed: boolean) => void) {
        interface Info {
            passed: boolean;
        }
        const info = autoJsStoreUtils.read<Info>('showSensorInfo', { passed: false });
        if (!info || !info.passed) {
            fallSensorCallbackRef.current = callback;
            setFallSensorInfoDialogOpen(true);
        } else {
            callback(true);
        }
    }

    function checkShowFallInfo(callback: (passed: boolean) => void) {
        checkShowFallSensorInfo(passed => {
            if (!passed) {
                callback(false);
                return;
            }
            checkShowFallForegroundServiceInfo(callback);
        })
    }

    function checkShowFallForegroundServiceInfo(callback: (passed: boolean) => void) {
        interface Info {
            passed: boolean;
        }
        const info = autoJsStoreUtils.read<Info>('showForegroundServiceInfo', { passed: false });
        if (!info || !info.passed) {
            fallForegroundServiceCallbackRef.current = callback;
            setFallForegroundServiceDialogOpen(true);
        } else {
            callback(true);
        }
    }

    //  SensorMonitor.tsx - 
    const handleFallSensorDialog = (passed: boolean) => {
        autoJsStoreUtils.write('showSensorInfo', { passed });
        setFallSensorInfoDialogOpen(false);
        if (fallSensorCallbackRef.current) {
            fallSensorCallbackRef.current(passed);
        }
    };

    const handleFallForegroundServiceDialog = (passed: boolean) => {
        autoJsStoreUtils.write('showForegroundServiceInfo', { passed });
        setFallForegroundServiceDialogOpen(false);
        if (fallForegroundServiceCallbackRef.current) {
            fallForegroundServiceCallbackRef.current(passed);
        }
    };

    // 
    function checkPrivacyAgreement(callback: (passed: boolean) => void): boolean {
        interface IPrivacyInfo {
            agree: boolean;
            disagreeTime: number;
        }
        
        const def: IPrivacyInfo = { agree: false, disagreeTime: 0 };
        const res = autoJsStoreUtils.read<IPrivacyInfo>(`privacy_main`, def);
        const value: IPrivacyInfo = res && 'agree' in res ? res : def;
        
        // 
        if (value.agree) {
            callback(true);
            return true;
        }
        
        // 
        privacyCallbackRef.current = callback;
        setPrivacyDialogOpen(true);
        return false;
    }

    // 
    const handlePrivacyDialogClose = (agreed?: boolean) => {
        setPrivacyDialogOpen(false);
        if (privacyCallbackRef.current) {
            privacyCallbackRef.current(agreed === true);
        }
    };

    //  SensorMonitor.tsx - 
    async function handleToggleFallMonitoring(enabled: boolean) {
        console.log('[] :', enabled ? '' : '');
        
        if (enabled) {
            console.log('[] ');
            
            // 1. 
            console.log('[] 1: ');
            const privacyPassed = checkPrivacyAgreement(passed => {
                console.log('[] :', passed ? '' : '');
                if (!passed) {
                    console.log('[] ');
                    return;
                }
                
                // 2. 
                console.log('[] 2: ');
                if (REQUIRE_LOGIN_FOR_FALL_DETECTION) {
                    console.log('[] ');
                    authService.showLoginDialog().then(appLoggedIn => {
                        console.log('[] :', appLoggedIn ? '' : '');
                        if (!appLoggedIn) {
                            console.warn('[] ');
                            alert("");
                            return;
                        }
                        
                        // 3. 
                        console.log('[] 3: ');
                        checkShowFallInfo(permissionPassed => {
                            console.log('[] :', permissionPassed ? '' : '');
                            if (!permissionPassed) {
                                console.log('[] ');
                                return;
                            }
                            
                            try {
                                console.log('[] ');
                                const script = `com.fanfanlo.emergencycall.manager.SensorManager.startMonitoring();`;
                                autoWebViewJs.callScript(script);
                                console.log('[] ');
                            } catch (error) {
                                console.error('[] :', error);
                                alert('');
                            }
                        });
                    });
                } else {
                    // 
                    console.log('[] ');
                    checkShowFallInfo(permissionPassed => {
                        console.log('[] ():', permissionPassed ? '' : '');
                        if (!permissionPassed) {
                            console.log('[] ()');
                            return;
                        }
                        
                        try {
                            console.log('[] ()');
                            const script = `com.fanfanlo.emergencycall.manager.SensorManager.startMonitoring();`;
                            autoWebViewJs.callScript(script);
                            console.log('[] ()');
                        } catch (error) {
                            console.error('[] ():', error);
                            alert('');
                        }
                    });
                }
            });
            
            // 
            if (privacyPassed) {
                console.log('[] ');
                
                // 2. 
                console.log('[]  - ');
                if (REQUIRE_LOGIN_FOR_FALL_DETECTION) {
                    try {
                        console.log('[] ()');
                        const appLoggedIn = await authService.showLoginDialog();
                        console.log('[] ():', appLoggedIn ? '' : '');
                        
                        if (!appLoggedIn) {
                            console.warn('[] ()');
                            alert("");
                            return;
                        }
                    } catch (error) {
                        console.error('[] :', error);
                        return;
                    }
                } else {
                    console.log('[]  - ');
                }
                
                // 3. 
                console.log('[]  - ');
                checkShowFallInfo(passed => {
                    console.log('[]  - :', passed ? '' : '');
                    
                    if (!passed) {
                        console.log('[]  - ');
                        return;
                    }
                    
                    try {
                        console.log('[]  - ');
                        const script = `com.fanfanlo.emergencycall.manager.SensorManager.startMonitoring();`;
                        autoWebViewJs.callScript(script);
                        console.log('[]  - ');
                    } catch (error) {
                        console.error('[]  - :', error);
                        alert('');
                    }
                });
            }
        } else {
            // 
            console.log('[] ');
            try {
                const script = `com.fanfanlo.emergencycall.manager.SensorManager.stopMonitoring();`;
                console.log('[] ');
                await autoWebViewJs.callScript(script);
                console.log('[] ');
            } catch (error) {
                console.error('[] :', error);
                alert('');
            }
        }
    }

    //  SensorMonitor.tsx - 
    useEffect(() => {
        const id = setInterval(() => {
            readFallIsMonitoring();
        }, 3000); // 3
        return () => clearInterval(id);
    }, []);

    // 
    const AnimationPlaceholder: React.FC<{ type?: 'fall' | 'shake' }> = ({ type }) => {
        const getAnimationContent = () => {
            switch (type) {
                case 'fall':
                    return {
                        icon: '',
                        title: '',
                        description: '',
                        color: theme.palette.error.main
                    };
                case 'shake':
                    return {
                        icon: '',
                        title: '',
                        description: '',
                        color: theme.palette.warning.main
                    };
                default:
                    return {
                        icon: '',
                        title: '',
                        description: '',
                        color: theme.palette.primary.main
                    };
            }
        };

        const content = getAnimationContent();

        return (
            <Box sx={{ 
                textAlign: 'center',
                color: 'text.secondary',
                px: 4
            }}>
                <Box sx={{ 
                    fontSize: 72, 
                    mb: 2,
                    animation: 'pulse 2s infinite',
                    '@keyframes pulse': {
                        '0%': { transform: 'scale(1)' },
                        '50%': { transform: 'scale(1.1)' },
                        '100%': { transform: 'scale(1)' }
                    }
                }}>
                    {content.icon}
                </Box>
                <Typography 
                    variant="h5" 
                    sx={{ 
                        mb: 1, 
                        fontWeight: 600,
                        color: content.color
                    }}
                >
                    {content.title}
                </Typography>
                <Typography 
                    variant="body1" 
                    sx={{ 
                        color: 'text.secondary',
                        lineHeight: 1.6
                    }}
                >
                    {content.description}
                </Typography>
                <Box sx={{
                    mt: 3,
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    gap: 1,
                    color: 'text.disabled'
                }}>
                    <AnimationIcon sx={{ fontSize: 20 }} />
                    <Typography variant="caption">
                        
                    </Typography>
                </Box>
            </Box>
        );
    };

    return (
        <Paper 
            sx={{ 
                mb: 2, 
                overflow: 'hidden',
                border: `1px solid ${alpha(theme.palette.primary.main, 0.2)}`,
                '&:hover': {
                    boxShadow: theme.shadows[4]
                }
            }}
        >
            {/*  */}
            <Box 
                sx={{ 
                    p: 2, 
                    display: 'flex', 
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    backgroundColor: alpha(theme.palette.primary.main, 0.05),
                    '&:hover': {
                        backgroundColor: alpha(theme.palette.primary.main, 0.1)
                    }
                }}
            >
                {/*  */}
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
                    <Typography 
                        variant="h6" 
                        sx={{ 
                            fontWeight: 600,
                            color: theme.palette.primary.main
                        }}
                    >
                        {title}
                    </Typography>
                    
                    {/* / */}
                    <FormControlLabel
                        control={
                            <Switch
                                checked={fallIsMonitoring}
                                onChange={(e) => handleToggleFallMonitoring(e.target.checked)}
                                size="small"
                                color="primary"
                            />
                        }
                        label={
                            <Typography variant="body2" color="text.secondary">
                                {fallIsMonitoring ? '' : ''}
                            </Typography>
                        }
                        onClick={(e) => e.stopPropagation()}
                        sx={{ margin: 0 }}
                    />
                </Box>

                {/* / */}
                <IconButton 
                    size="small"
                    onClick={() => onToggleExpanded(!isExpanded)}
                    sx={{ 
                        color: theme.palette.primary.main,
                        transform: isExpanded ? 'rotate(180deg)' : 'rotate(0deg)',
                        transition: 'transform 0.2s ease-in-out'
                    }}
                >
                    <ExpandMore />
                </IconButton>
            </Box>
            
            {/*  */}
            <Box>
                {/*  -  */}
                <Collapse in={!isExpanded}>
                    <Box sx={{ 
                        height: 300,
                        width: '100%',
                        display: 'flex', 
                        alignItems: 'center', 
                        justifyContent: 'center',
                        backgroundColor: alpha(theme.palette.background.paper, 0.8),
                        borderTop: `1px solid ${theme.palette.divider}`,
                        position: 'relative',
                        overflow: 'hidden'
                    }}>
                        {/* <FallRescueAnimation type={animationType} /> */}
                        <Box>
                            <FallDownVideoPlayer/>
                        </Box>
                    </Box>
                </Collapse>
                
                {/*  -  */}
                <Collapse in={isExpanded}>
                    <Box sx={{ 
                        borderTop: `1px solid ${theme.palette.divider}`,
                        backgroundColor: theme.palette.background.paper
                    }}>
                        <FallDetectionSettings voiceSettingsRef={voiceSettingsRef} />
                    </Box>
                </Collapse>
            </Box>

            {/*  SensorMonitor.tsx -  */}
            <Dialog
                open={fallSensorInfoDialogOpen}
                onClose={() => handleFallSensorDialog(false)}
                aria-labelledby="fall-sensor-dialog-title"
                aria-describedby="fall-sensor-dialog-description"
            >
                <DialogTitle id="fall-sensor-dialog-title">
                    {""}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="fall-sensor-dialog-description">
                        
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => handleFallSensorDialog(false)}></Button>
                    <Button onClick={() => handleFallSensorDialog(true)} autoFocus>
                        
                    </Button>
                </DialogActions>
            </Dialog>

            {/*  SensorMonitor.tsx -  */}
            <Dialog
                open={fallForegroundServiceDialogOpen}
                onClose={() => handleFallForegroundServiceDialog(false)}
                aria-labelledby="fall-foreground-dialog-title"
                aria-describedby="fall-foreground-dialog-description"
            >
                <DialogTitle id="fall-foreground-dialog-title">
                    {""}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="fall-foreground-dialog-description">
                        
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => handleFallForegroundServiceDialog(false)}></Button>
                    <Button onClick={() => handleFallForegroundServiceDialog(true)} autoFocus>
                        
                    </Button>
                </DialogActions>
            </Dialog>

            {/*  */}
            <PrivacyDialogDisplay
                open={privacyDialogOpen}
                onClose={handlePrivacyDialogClose}
                type="main"
                area="zh-cn"
            />
        </Paper>
    );
};

export default FallDetectionWrapper;