import React, { useState } from 'react';
import {
  Button,
  Snackbar,
  Alert,
  Fab,
  Tooltip
} from '@mui/material';
import {
  Stop as StopIcon,
  VolumeOff as VolumeOffIcon
} from '@mui/icons-material';
import { autoWebViewJs } from "@droid/android";
import { Log } from "@fanfanlo";
import { trackingPrint } from "@app/model";
import { stopAllHandler } from './stopAllHandler';

const fileLog = new Log(false, 'EmergencyStopButton');

interface EmergencyStopButtonProps {
  variant?: 'button' | 'fab';
  size?: 'small' | 'medium' | 'large';
  fullWidth?: boolean;
}

/**
 * 
 * 
 */
const EmergencyStopButton: React.FC<EmergencyStopButtonProps> = ({ 
  variant = 'button',
  size = 'medium',
  fullWidth = false
}) => {
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState<{ type: 'success' | 'error'; text: string } | null>(null);

  /**
   * Android WebView
   */
  const isAndroidWebView = (): boolean => {
    try {
      return !!(autoWebViewJs && typeof autoWebViewJs.callScript === 'function');
    } catch (e) {
      fileLog.error('WebView:', e);
      return false;
    }
  };

  /**
   * 
   */
  const handleStopAllEmergencyAudio = async (): Promise<void> => {
    setLoading(true);
    setMessage(null);

    // 
    trackingPrint({params:{
      type:"",
      title:"",
      subtitle:"-",
      extra:{
        buttonType: variant,
        timestamp: Date.now(),
        isAndroidWebView: isAndroidWebView()
      }
    }});

    try {
      const bl = stopAllHandler();
      if (bl){
        // 
        trackingPrint({params:{
          type:"",
          title:"",
          subtitle:"",
          extra:{
            result: "success",
            timestamp: Date.now()
          }
        }});
        
        setMessage({
          type: 'success',
          text: ''
        });
      }else{
        // 
        trackingPrint({params:{
          type:"",
          title:"",
          subtitle:"",
          extra:{
            result: "failed",
            timestamp: Date.now(),
            reason: "stopAllHandlerfalse"
          }
        }});
        
        setMessage({
          type: 'error',
          text: ``
        });
      }
    } catch (error) {
      // 
      trackingPrint({params:{
        type:"",
        title:"",
        subtitle:"",
        extra:{
          error: error instanceof Error ? error.message : String(error),
          stack: error instanceof Error ? error.stack : undefined,
          timestamp: Date.now()
        }
      }});
      
      setMessage({
        type: 'error',
        text: ''
      });
    }
    
    setLoading(false);
  };

  /**
   * 
   */
  const handleCloseMessage = () => {
    setMessage(null);
  };

  // 
  if (variant === 'fab') {
    return (
      <>
        <Tooltip title="" placement="left">
          <Fab
            color="error"
            onClick={handleStopAllEmergencyAudio}
            disabled={loading}
            size={size}
            sx={{
              position: 'fixed',
              bottom: 80,
              right: 16,
              zIndex: 1000
            }}
          >
            {loading ? <VolumeOffIcon /> : <StopIcon />}
          </Fab>
        </Tooltip>

        <Snackbar
          open={!!message}
          autoHideDuration={4000}
          onClose={handleCloseMessage}
          anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
        >
          <Alert 
            onClose={handleCloseMessage} 
            severity={message?.type || 'info'}
            sx={{ width: '100%' }}
          >
            {message?.text}
          </Alert>
        </Snackbar>
      </>
    );
  }

  // 
  return (
    <>
      <Button
        variant="contained"
        color="error"
        size={size}
        fullWidth={fullWidth}
        startIcon={loading ? <VolumeOffIcon /> : <StopIcon />}
        onClick={handleStopAllEmergencyAudio}
        disabled={loading}
        sx={{ mb: 1 }}
      >
         
      </Button>

      <Snackbar
        open={!!message}
        autoHideDuration={4000}
        onClose={handleCloseMessage}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert 
          onClose={handleCloseMessage} 
          severity={message?.type || 'info'}
          sx={{ width: '100%' }}
        >
          {message?.text}
        </Alert>
      </Snackbar>
    </>
  );
};

export default EmergencyStopButton;