import React, { useState, useEffect, useCallback, useRef, useMemo } from 'react';
import {
    Box,
    Paper,
    Typography,
    Switch,
    FormControlLabel,
    TextField,
    Select,
    MenuItem,
    FormControl,
    InputLabel,
    Chip,
    Alert,
    Grid,
    Divider,
    Button,
    Tooltip,
    Collapse,
    IconButton
} from '@mui/material';
import { 
    VolumeUp as VolumeUpIcon,
    Vibration as VibrationIcon,
    PlayArrow,
    Stop,
    Delete,
    DragIndicator as DragIndicatorIcon
} from '@mui/icons-material';
import { autoJsStoreUtils, autoWebViewJs } from '@droid/android';
import { useProxyWatch } from '@fanfanlo';
import { user } from '@app/model';
import _ from 'lodash';
import { DndContext, closestCenter, KeyboardSensor, PointerSensor, TouchSensor, useSensor, useSensors, DragEndEvent } from '@dnd-kit/core';
import { arrayMove, SortableContext, sortableKeyboardCoordinates, useSortable, verticalListSortingStrategy } from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import { EmergencyTTSSettings } from './components';
import { VoiceEmergencySettingsRef } from './components/VoiceEmergencySettings';
import toast from 'react-hot-toast';

import VoiceEmergencySettings from './components/VoiceEmergencySettings';

const SHAKE_CONFIG_KEY = "shake_emergency_config";

// 
const THRESHOLD_MIN = 7;
const THRESHOLD_MAX = 15;
const THRESHOLD_DEFAULT = 9;

// 
interface PreAlarmAction {
    id: string;
    type: 'sound' | 'vibration';
    duration: number;
    resourceId?: string;
}

// 
interface ShakeConfig {
    enabled: boolean;
    shakeCount: number;
    timeWindowMs: number;
    emergencyType: string;
    threshold: number;
    // 
    baseMotionThreshold: number;
    directionChangeThreshold: number;
    minMotionInterval: number;
    cooldownDuration: number;
    vibrationFeedback: boolean;
    soundFeedback: boolean;
    actions: PreAlarmAction[];
    maxWaitDuration: number;
    version: number;
}

interface ShakeDetectionSettingsProps {
    voiceSettingsRef?: React.RefObject<VoiceEmergencySettingsRef>;
    config:ShakeConfig
}
// 
const defaultConfig: ShakeConfig = {
    enabled: false,
    shakeCount: 3,
    timeWindowMs: 2000,
    emergencyType: "medical",
    threshold: THRESHOLD_DEFAULT,
    // 
    baseMotionThreshold: 3.5,
    directionChangeThreshold: 0.2,
    minMotionInterval: 200,
    cooldownDuration: 6000,
    vibrationFeedback: true,
    soundFeedback: false,
    actions: [],
    maxWaitDuration: 10000,
    version: 1
};

// 
const emergencyTypeOptions = [
    { value: "medical", label: "" },
    { value: "security", label: "" }
];

// 
const defaultSoundResources = [
    { id: 'siren_gentle', name: '' },
    { id: 'beep_short', name: '' },
    { id: 'alarm_clock', name: '' },
];

function loadConfig(): ShakeConfig {
    const conf = autoJsStoreUtils.read(SHAKE_CONFIG_KEY, {});
    const config = _.merge(defaultConfig, conf);
    return config;
}

const ShakeDetectionSettings: React.FC<ShakeDetectionSettingsProps> = ({ config: conf, voiceSettingsRef }) => {
    const [config, setConfig] = useState<ShakeConfig>(defaultConfig);
    const [isLoading, setIsLoading] = useState(true);
    const [lastSaveTime, setLastSaveTime] = useState<string>('');
    const [playingSoundId, setPlayingSoundId] = useState<string | null>(null);
    
    // 
    const sensors = useSensors(
        useSensor(PointerSensor, {
            activationConstraint: {
                distance: 8,
            },
        }),
        useSensor(TouchSensor, {
            activationConstraint: {
                delay: 250,
                tolerance: 5,
            },
        }),
        useSensor(KeyboardSensor, {
            coordinateGetter: sortableKeyboardCoordinates,
        })
    );
    
    // 
    const [userToken] = useProxyWatch(user, 'data.storeData.user_token', user.data.storeData.user_token);
    const isLoggedIn = !!userToken;
    
    // 
    const [tempValues, setTempValues] = useState<{
        shakeCount?: string;
        timeWindowMs?: string;
        threshold?: string;
        baseMotionThreshold?: string;
        directionChangeThreshold?: string;
        minMotionInterval?: string;
        cooldownDuration?: string;
    }>({});
    
    const localVoiceSettingsRef = useRef<VoiceEmergencySettingsRef>(null);
    const effectiveVoiceSettingsRef = voiceSettingsRef || localVoiceSettingsRef;

    /**
     * Android
     */
    const loadConfigFromAndroid = useCallback(() => {
        console.log('Android...');
        const conf = loadConfig();
        console.log(SHAKE_CONFIG_KEY, 'string', JSON.stringify(conf));
        setConfig(conf);
        setIsLoading(false);
    }, []);

    /**
     * 
     */
    const validateConfig = (config: ShakeConfig): boolean => {
        return (
            config.shakeCount >= 1 && config.shakeCount <= 10 &&
            config.timeWindowMs > 0 && config.timeWindowMs <= 10000 &&
            ['medical', 'security'].includes(config.emergencyType) &&
            typeof config.threshold === 'number' &&
            config.threshold >= 0 &&
            // 
            typeof config.baseMotionThreshold === 'number' && config.baseMotionThreshold >= 0.1 &&
            typeof config.directionChangeThreshold === 'number' && 
            config.directionChangeThreshold >= -1.0 && config.directionChangeThreshold <= 1.0 &&
            typeof config.minMotionInterval === 'number' && config.minMotionInterval >= 50 &&
            typeof config.cooldownDuration === 'number' && config.cooldownDuration >= 1000 &&
            config.version > 0
        );
    };

    /**
     * Android
     */
    const saveConfigToAndroid = useCallback((newConfig: ShakeConfig) => {
        try {
            console.log('Android:', newConfig);
            
            // 
            if (!validateConfig(newConfig)) {
                console.error('');
                return;
            }

            const config = loadConfig();
            newConfig.enabled = config.enabled;
            //  maxWaitDuration
            newConfig.maxWaitDuration = newConfig.actions.reduce((sum, action) => sum + action.duration, 0);
            // JSON
            const configJson = JSON.stringify(newConfig);
            
            // Base64
            const base64Config = btoa(unescape(encodeURIComponent(configJson)));
            
            // RhinoAndroid
            const script = `
                var result = com.fanfanlo.emergencycall.controller.ShakeManager.updateShakeConfig("${base64Config}");
                result;
            `;
            autoWebViewJs.callScript(script);
            setLastSaveTime(new Date().toLocaleTimeString());
            
        } catch (error) {
            console.error(':', error);
        }
    }, []);

    /**
     * 
     */
    const updateConfig = useCallback((updates: Partial<ShakeConfig>) => {
        const newConfig = { ...config, ...updates };
        setConfig(newConfig);
        
        // 
        if (validateConfig(newConfig)) {
            saveConfigToAndroid(newConfig);
        }
    }, [config, saveConfigToAndroid]);

    /**
     * 
     */
    const handleNumberInput = useCallback((field: 'shakeCount' | 'timeWindowMs' | 'threshold' | 'baseMotionThreshold' | 'directionChangeThreshold' | 'minMotionInterval' | 'cooldownDuration', value: string) => {
        // 
        setTempValues(prev => ({ ...prev, [field]: value }));
        
        // 
        const timerId = setTimeout(() => {
            if (field === 'threshold' || field === 'baseMotionThreshold' || field === 'directionChangeThreshold') {
                // 
                const numValue = parseFloat(value);
                if (!isNaN(numValue) && numValue >= 0) {
                    updateConfig({ [field]: numValue });
                    setTempValues(prev => ({ ...prev, [field]: undefined }));
                } else if (value === '') {
                    // 
                    setTimeout(() => {
                        if (tempValues[field] === '') {
                            setTempValues(prev => ({ ...prev, [field]: undefined }));
                        }
                    }, 2000);
                } else {
                    // 
                    setTimeout(() => {
                        setTempValues(prev => ({ ...prev, [field]: undefined }));
                    }, 2000);
                }
            } else {
                // 
                const numValue = parseInt(value);
                if (!isNaN(numValue) && numValue > 0) {
                    if (field === 'shakeCount' && numValue >= 1 && numValue <= 10) {
                        updateConfig({ [field]: numValue });
                        setTempValues(prev => ({ ...prev, [field]: undefined }));
                    } else if (field === 'timeWindowMs' && numValue >= 1000 && numValue <= 10000) {
                        updateConfig({ [field]: numValue });
                        setTempValues(prev => ({ ...prev, [field]: undefined }));
                    } else if (field === 'minMotionInterval' && numValue >= 50 && numValue <= 2000) {
                        updateConfig({ [field]: numValue });
                        setTempValues(prev => ({ ...prev, [field]: undefined }));
                    } else if (field === 'cooldownDuration' && numValue >= 1000 && numValue <= 60000) {
                        updateConfig({ [field]: numValue });
                        setTempValues(prev => ({ ...prev, [field]: undefined }));
                    } else {
                        setTimeout(() => {
                            setTempValues(prev => ({ ...prev, [field]: undefined }));
                        }, 2000);
                    }
                } else if (value === '') {
                    setTimeout(() => {
                        if (tempValues[field] === '') {
                            setTempValues(prev => ({ ...prev, [field]: undefined }));
                        }
                    }, 2000);
                } else {
                    setTimeout(() => {
                        setTempValues(prev => ({ ...prev, [field]: undefined }));
                    }, 2000);
                }
            }
        }, 500); // 500ms

        return () => clearTimeout(timerId);
    }, [updateConfig, tempValues]);

    /**
     * 
     */
    const handleAddPreAlarmAction = useCallback((type: 'sound' | 'vibration') => {
        const newAction: PreAlarmAction = {
            id: `action-${Date.now()}`,
            type,
            duration: type === 'vibration' ? 1000 : 2000,
            ...(type === 'sound' && { resourceId: defaultSoundResources[0].id }),
        };
        updateConfig({
            actions: [...config.actions, newAction],
        });
    }, [config.actions, updateConfig]);

    const handleUpdatePreAlarmAction = useCallback((id: string, updates: Partial<Omit<PreAlarmAction, 'id'>>) => {
        updateConfig({
            actions: config.actions.map((action) =>
                action.id === id ? { ...action, ...updates } : action
            ),
        });
    }, [config.actions, updateConfig]);

    const handleRemovePreAlarmAction = useCallback((id: string) => {
        updateConfig({
            actions: config.actions.filter((action) => action.id !== id),
        });
    }, [config.actions, updateConfig]);

    const handlePreviewSound = useCallback((resourceId?: string) => {
        if (!resourceId) return;
        handleStopSound();
        
        console.log(`[ShakeConfig] Previewing sound: ${resourceId}`);
        
        if ((window as any).SoundManager) {
            (window as any).SoundManager.previewSound(resourceId);
            setPlayingSoundId(resourceId);
        } else {
            console.warn('SoundManager not available');
        }
    }, []);

    const handleStopSound = useCallback(() => {
        if ((window as any).SoundManager) {
            (window as any).SoundManager.stopPreview();
        }
        setPlayingSoundId(null);
    }, []);

    /**
     * 
     */
    const handleDragEnd = useCallback((event: DragEndEvent) => {
        const { active, over } = event;
        if (!over || active.id === over.id) return;

        const oldIndex = config.actions.findIndex((a) => a.id === active.id);
        const newIndex = config.actions.findIndex((a) => a.id === over.id);

        if (oldIndex !== -1 && newIndex !== -1) {
            updateConfig({
                actions: arrayMove(config.actions, oldIndex, newIndex),
            });
        }
    }, [config.actions, updateConfig]);

    /**
     * 
     */
    const SortablePreAlarmActionItem: React.FC<{
        action: PreAlarmAction;
        isPlaying: boolean;
        onUpdate: (id: string, updates: Partial<Omit<PreAlarmAction, 'id'>>) => void;
        onRemove: (id: string) => void;
        onPreview: (resourceId?: string) => void;
        onStopPreview: () => void;
        disabled: boolean;
    }> = ({ action, isPlaying, onUpdate, onRemove, onPreview, onStopPreview, disabled }) => {
        const { attributes, listeners, setNodeRef, transform, transition, isDragging } = useSortable({ id: action.id });
        
        // 
        const [localDuration, setLocalDuration] = useState(action.duration);
        
        // 
        const debouncedUpdate = useMemo(
            () => _.debounce((id: string, updates: Partial<Omit<PreAlarmAction, 'id'>>) => {
                onUpdate(id, updates);
                toast.success('');
            }, 1000), // 1
            [onUpdate]
        );
        
        // 
        const handleDurationChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
            const value = parseInt(e.target.value, 10) || 0;
            setLocalDuration(value); // 
            debouncedUpdate(action.id, { duration: value }); // 
        }, [action.id, debouncedUpdate]);
        
        //  action.duration 
        useEffect(() => {
            setLocalDuration(action.duration);
        }, [action.duration]);
        
        // 
        useEffect(() => {
            return () => {
                debouncedUpdate.cancel();
            };
        }, [debouncedUpdate]);

        const style = {
            transform: CSS.Transform.toString(transform),
            transition,
            opacity: isDragging ? 0.8 : 1,
            zIndex: isDragging ? 1 : 'auto',
        };

        return (
            <Paper
                ref={setNodeRef}
                style={style}
                elevation={isDragging ? 3 : 1}
                sx={{
                    p: 2,
                    mb: 1,
                    display: 'flex',
                    alignItems: 'center',
                    gap: 2,
                    userSelect: 'none',
                    opacity: disabled ? 0.6 : 1
                }}
            >
                <Box {...attributes} {...listeners} sx={{ display: 'flex', alignItems: 'center', cursor: 'grab', touchAction: 'none' }}>
                    <DragIndicatorIcon />
                </Box>
                
                <Box sx={{ flexGrow: 1 }}>
                    <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-start', gap: 1.5 }}>
                        <Typography variant="subtitle2">
                            {action.type === 'sound' ? '' : ''}
                        </Typography>
                        
                        <TextField
                            size="small"
                            type="number"
                            label=" (ms)"
                            value={localDuration}
                            onChange={handleDurationChange}
                            disabled={disabled}
                            sx={{ width: '100%' }}
                        />
                        
                        {action.type === 'sound' && (
                            <FormControl size="small" sx={{ width: '100%' }}>
                                <InputLabel></InputLabel>
                                <Select
                                    value={action.resourceId || ''}
                                    label=""
                                    onChange={(e) => onUpdate(action.id, { resourceId: e.target.value })}
                                    disabled={disabled}
                                    startAdornment={
                                        <Tooltip title={isPlaying ? "" : ""}>
                                            <IconButton
                                                size="small"
                                                onClick={() => isPlaying ? onStopPreview() : onPreview(action.resourceId)}
                                                sx={{ mr: 1 }}
                                                disabled={disabled}
                                            >
                                                {isPlaying ? <Stop fontSize="small" /> : <PlayArrow fontSize="small" />}
                                            </IconButton>
                                        </Tooltip>
                                    }
                                >
                                    {defaultSoundResources.map((sound) => (
                                        <MenuItem key={sound.id} value={sound.id}>
                                            {sound.name}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </FormControl>
                        )}
                    </Box>
                </Box>
                
                <IconButton onClick={() => onRemove(action.id)} size="small" color="error" disabled={disabled}>
                    <Delete />
                </IconButton>
            </Paper>
        );
    };

    // 
    useEffect(() => {
        loadConfigFromAndroid();
    }, [loadConfigFromAndroid]);

    // 
    if (isLoading) {
        return (
            <Paper sx={{ p: 2 }}>
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    <Typography variant="body2" color="text.secondary">
                        ...
                    </Typography>
                </Box>
            </Paper>
        );
    }

    return (
        <Box sx={{ p: 2 }}>
            {/*  */}
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 2 }}>
                {/* <FormControlLabel
                    control={
                        <Switch
                            checked={config.enabled}
                            onChange={(e) => updateConfig({ enabled: e.target.checked })}
                            size="small"
                            disabled={!isLoggedIn}
                        />
                    }
                    label={config.enabled ? '' : ''}
                />
                {lastSaveTime && (
                    <Typography variant="caption" color="text.secondary">
                         {lastSaveTime}
                    </Typography>
                )} */}
            </Box>

            {/*  */}
            {!isLoggedIn && (
                <Alert severity="warning" sx={{ mb: 2 }}>
                    <Typography variant="body2">
                        
                    </Typography>
                </Alert>
            )}

            {/*  */}
            <Grid container spacing={2} sx={{ mb: 3 }}>
                {/*  */}
                <Grid item xs={12} sm={6}>
                    <FormControl fullWidth disabled={ !isLoggedIn}>
                        <InputLabel></InputLabel>
                        <Select
                            value={config.emergencyType}
                            label=""
                            onChange={(e) => updateConfig({ emergencyType: e.target.value })}
                        >
                            {emergencyTypeOptions.map(option => (
                                <MenuItem key={option.value} value={option.value}>
                                    {option.label}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                </Grid>
            </Grid>

            {/*  */}
            <Typography variant="h6" gutterBottom></Typography>
            <Grid container spacing={2} sx={{ mb: 3 }}>
                {/*  */}
                <Grid item xs={12} sm={4}>
                    <TextField
                        fullWidth
                        label=""
                        type="number"
                        inputProps={{ min: 1, max: 10 }}
                        value={tempValues.shakeCount !== undefined ? tempValues.shakeCount : config.shakeCount}
                        onChange={(e) => handleNumberInput('shakeCount', e.target.value)}
                        disabled={ !isLoggedIn}
                        helperText="1-10"
                        error={tempValues.shakeCount !== undefined && (tempValues.shakeCount === '' || parseInt(tempValues.shakeCount) < 1 || parseInt(tempValues.shakeCount) > 10)}
                    />
                </Grid>

                {/*  */}
                <Grid item xs={12} sm={4}>
                    <TextField
                        fullWidth
                        label="()"
                        type="number"
                        inputProps={{ min: 1000, max: 10000, step: 500 }}
                        value={tempValues.timeWindowMs !== undefined ? tempValues.timeWindowMs : config.timeWindowMs}
                        onChange={(e) => handleNumberInput('timeWindowMs', e.target.value)}
                        disabled={ !isLoggedIn}
                        helperText="1000-10000ms"
                        error={tempValues.timeWindowMs !== undefined && (tempValues.timeWindowMs === '' || parseInt(tempValues.timeWindowMs) < 1000 || parseInt(tempValues.timeWindowMs) > 10000)}
                    />
                </Grid>

                {/*  */}
                <Grid item xs={12} sm={4}>
                    <TextField
                        fullWidth
                        label=""
                        type="number"
                        inputProps={{ min: 0, step: 0.1 }}
                        value={tempValues.threshold !== undefined ? tempValues.threshold : config.threshold}
                        onChange={(e) => handleNumberInput('threshold', e.target.value)}
                        disabled={ !isLoggedIn}
                        helperText={`${THRESHOLD_MIN}-${THRESHOLD_MAX}`}
                        error={tempValues.threshold !== undefined && (tempValues.threshold === '' || parseFloat(tempValues.threshold) < 0)}
                    />
                </Grid>
            </Grid>

            {/*  */}
            <Typography variant="h6" gutterBottom sx={{ mt: 3 }}></Typography>
            <Grid container spacing={2} sx={{ mb: 3 }}>
                {/*  */}
                <Grid item xs={12} sm={6}>
                    <TextField
                        fullWidth
                        label=""
                        type="number"
                        inputProps={{ min: 0.1, step: 0.1 }}
                        value={tempValues.baseMotionThreshold !== undefined ? tempValues.baseMotionThreshold : config.baseMotionThreshold}
                        onChange={(e) => handleNumberInput('baseMotionThreshold', e.target.value)}
                        disabled={!isLoggedIn}
                        helperText=": 2.0-8.0"
                        error={tempValues.baseMotionThreshold !== undefined && (tempValues.baseMotionThreshold === '' || parseFloat(tempValues.baseMotionThreshold) < 0.1)}
                    />
                </Grid>

                {/*  */}
                <Grid item xs={12} sm={6}>
                    <TextField
                        fullWidth
                        label=""
                        type="number"
                        inputProps={{ min: -1.0, max: 1.0, step: 0.01 }}
                        value={tempValues.directionChangeThreshold !== undefined ? tempValues.directionChangeThreshold : config.directionChangeThreshold}
                        onChange={(e) => handleNumberInput('directionChangeThreshold', e.target.value)}
                        disabled={!isLoggedIn}
                        helperText=": -0.50.5"
                        error={tempValues.directionChangeThreshold !== undefined && (tempValues.directionChangeThreshold === '' || parseFloat(tempValues.directionChangeThreshold) < -1.0 || parseFloat(tempValues.directionChangeThreshold) > 1.0)}
                    />
                </Grid>

                {/*  */}
                <Grid item xs={12} sm={6}>
                    <TextField
                        fullWidth
                        label=" ()"
                        type="number"
                        inputProps={{ min: 50, max: 2000, step: 10 }}
                        value={tempValues.minMotionInterval !== undefined ? tempValues.minMotionInterval : config.minMotionInterval}
                        onChange={(e) => handleNumberInput('minMotionInterval', e.target.value)}
                        disabled={!isLoggedIn}
                        helperText=": 200-800ms"
                        error={tempValues.minMotionInterval !== undefined && (tempValues.minMotionInterval === '' || parseInt(tempValues.minMotionInterval) < 50 || parseInt(tempValues.minMotionInterval) > 2000)}
                    />
                </Grid>

                {/*  */}
                <Grid item xs={12} sm={6}>
                    <TextField
                        fullWidth
                        label=" ()"
                        type="number"
                        inputProps={{ min: 1000, max: 60000, step: 100 }}
                        value={tempValues.cooldownDuration !== undefined ? tempValues.cooldownDuration : config.cooldownDuration}
                        onChange={(e) => handleNumberInput('cooldownDuration', e.target.value)}
                        disabled={!isLoggedIn}
                        helperText=": 3000-15000ms"
                        error={tempValues.cooldownDuration !== undefined && (tempValues.cooldownDuration === '' || parseInt(tempValues.cooldownDuration) < 1000 || parseInt(tempValues.cooldownDuration) > 60000)}
                    />
                </Grid>
            </Grid>

            <Divider sx={{ my: 3 }} />

            {/*  */}
            <Typography variant="h6" gutterBottom></Typography>
            <Box sx={{ mb: 3 }}>
                {/*  */}
                <Collapse in={config.actions.length > 0 || true}>
                    <Box sx={{ mt: 2, pl: 4, borderLeft: '2px solid', borderColor: 'primary.main' }}>
                        {/* <Grid container spacing={2} sx={{ mb: 2 }}>
                            <Grid item xs={12} sm={6}>
                                <TextField
                                    fullWidth
                                    size="small"
                                    type="number"
                                    label="(ms)"
                                    value={config.maxWaitDuration}
                                    onChange={(e) => updateConfig({ maxWaitDuration: parseInt(e.target.value, 10) || 0 })}
                                    disabled={ !isLoggedIn}
                                    helperText=""
                                />
                            </Grid>
                        </Grid> */}
                        
                        <Box sx={{ mb: 2 }}>
                            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 1 }}>
                                <Typography variant="body2" fontWeight="medium">
                                     ({config.actions.length})
                                </Typography>
                                <Box sx={{ display: 'flex', gap: 1 }}>
                                    <Button
                                        variant="outlined"
                                        size="small"
                                        startIcon={<VolumeUpIcon />}
                                        onClick={() => handleAddPreAlarmAction('sound')}
                                        disabled={ !isLoggedIn}
                                    >
                                        
                                    </Button>
                                    <Button
                                        variant="outlined"
                                        size="small"
                                        startIcon={<VibrationIcon />}
                                        onClick={() => handleAddPreAlarmAction('vibration')}
                                        disabled={ !isLoggedIn}
                                    >
                                        
                                    </Button>
                                </Box>
                            </Box>
                            
                            {config.actions.length === 0 ? (
                                <Box sx={{ 
                                    p: 2, 
                                    border: '1px dashed', 
                                    borderColor: 'divider', 
                                    borderRadius: 1, 
                                    textAlign: 'center',
                                    color: 'text.secondary'
                                }}>
                                    <Typography variant="body2"></Typography>
                                </Box>
                            ) : (
                                <DndContext
                                    sensors={sensors}
                                    collisionDetection={closestCenter}
                                    onDragEnd={handleDragEnd}
                                >
                                    <SortableContext
                                        items={config.actions.map((a) => a.id)}
                                        strategy={verticalListSortingStrategy}
                                    >
                                        {config.actions.map((action) => (
                                            <SortablePreAlarmActionItem
                                                key={action.id}
                                                action={action}
                                                isPlaying={playingSoundId === action.resourceId}
                                                onUpdate={handleUpdatePreAlarmAction}
                                                onRemove={handleRemovePreAlarmAction}
                                                onPreview={handlePreviewSound}
                                                onStopPreview={handleStopSound}
                                                disabled={ !isLoggedIn}
                                            />
                                        ))}
                                    </SortableContext>
                                </DndContext>
                            )}
                        </Box>
                    </Box>
                </Collapse>
                
                {/*  */}
                <FormControlLabel
                    control={
                        <Switch
                            checked={config.vibrationFeedback}
                            onChange={(e) => updateConfig({ vibrationFeedback: e.target.checked })}
                            disabled={!isLoggedIn}
                        />
                    }
                    label=""
                />
                
                <FormControlLabel
                    control={
                        <Switch
                            checked={config.soundFeedback}
                            onChange={(e) => updateConfig({ soundFeedback: e.target.checked })}
                            disabled={!isLoggedIn}
                        />
                    }
                    label=""
                />
            </Box>

            <Divider sx={{ my: 3 }} />

            <Box sx={{ mb: 3 }}>
                <VoiceEmergencySettings ref={effectiveVoiceSettingsRef} />
            </Box>
            {/*  */}
            {/* <Box sx={{ mb: 3 }}>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>
                <EmergencyTTSSettings />
            </Box> */}

            {/*  */}
            <Alert severity="info" sx={{ mt: 2 }}>
                <Typography variant="body2" component="div">
                    <strong></strong>
                    <ul style={{ margin: '8px 0', paddingLeft: '20px' }}>
                        <li>{config.timeWindowMs}{config.shakeCount}</li>
                        <li>{config.emergencyType === 'medical' ? '' : ''}</li>
                        <li>{config.threshold}{THRESHOLD_MIN}-{THRESHOLD_MAX}</li>
                        <li>{config.actions.length > 0 ? `${config.maxWaitDuration}${config.actions.length}` : ''}</li>
                    </ul>
                </Typography>
            </Alert>
        </Box>
    );
};

export default ShakeDetectionSettings;