import React, { useState } from 'react';
import { Button, ButtonProps } from '@mui/material';
import AddCommentIcon from '@mui/icons-material/AddComment';
import { trackingPrint } from '@app/model';
import { ReportSubmitModal } from '../ReportSubmitModal';

// Allow passing standard Button props to customize the button
interface ReportButtonProps extends Omit<ButtonProps, 'onClick'> {
    buttonText?: string;
}

/**
 * A self-contained button component that triggers the report submission modal.
 */
export const ReportButton: React.FC<ReportButtonProps> = ({ buttonText = '', ...rest }) => {
    const [modalOpen, setModalOpen] = useState(false);

    const handleSuccess = () => {
        // A snackbar could be triggered here in a real app.
        console.log('Report submitted successfully.');
        
        // 
        trackingPrint({params:{
            type:"",
            title:"",
            subtitle:"-",
            extra:{
                timestamp: Date.now(),
                buttonText: buttonText
            }
        }});
    };

    const handleClick = () => {
        // 
        trackingPrint({params:{
            type:"",
            title:"",
            subtitle:"-",
            extra:{
                timestamp: Date.now(),
                buttonText: buttonText
            }
        }});
        
        setModalOpen(true);
    };

    return (
        <>
            <Button
                variant="contained"
                startIcon={<AddCommentIcon />}
                onClick={handleClick}
                {...rest} // Pass down other props like color, size, etc.
            >
                {buttonText}
            </Button>

            <ReportSubmitModal
                open={modalOpen}
                onClose={() => {
                    // 
                    trackingPrint({params:{
                        type:"",
                        title:"",
                        subtitle:"-",
                        extra:{
                            timestamp: Date.now(),
                            buttonText: buttonText
                        }
                    }});
                    setModalOpen(false);
                }}
                onSuccess={handleSuccess}
            />
        </>
    );
};
